import React from 'react';
import SelectionButton from '../components/SelectionButton';

const RadioButtonPrescription = ({
    titleHeader,
    choices
}) => {
    return(
        <div className='radio-button-prescription'>
            <h4>{titleHeader}</h4>
            <div className="rdb-group">
                {
                    choices.map((choice, j)=>{
                        const {text} = choice
                        return (
                            <SelectionButton borderDisplay={false} key={j} title={text} handler={()=>{}}
                            parameter={titleHeader} type="radio"/>
                        )
                    })
                }
            </div>
        </div>
    )
}

export default RadioButtonPrescription;