import React, { useContext } from "react";
import NECategoryContainer from './NECategoryContainer';
import NEQuestionsContainer from './NEQuestionsContainer';
import { AppContext } from "../context/AppContext";

const NEContainer = () => {
  const { appState } = useContext(AppContext);
  const { needExplorerController } = appState;
  const { progress } = needExplorerController;
  return (
    
    <div className="need-explorer-container">
      <div className="need-explorer-container__left-panel">
        <NECategoryContainer/>
        <div className="progress-bar">
          <div className="header">
            <strong>Patient</strong>
            <span>{progress}%</span>
          </div>
            <progress value={progress} min="10" max="100"></progress>
        </div>
      </div>
      <div className="need-explorer-container__right-panel grid">
        <NEQuestionsContainer/>
      </div>
    </div>
  );
}

export default NEContainer;