import PairContainerGroup from "../containers/PairContainerGroup";

import Icon from "../components/Icon";
const SummaryContainer = ({pair, items}) => {
    return (
      <div className="summary-container">
        <div className="summary-container__header">
          <h2>{pair === 1 ? "CUSTOMIZED OFFER" : ""}</h2>
          <div className="pair-holder">
            <p>Pair #{pair}</p>
          </div>
        </div>
        <div className="holder">
          <div className="holder--one">
            {items.map((item, i) => {
              return (
                <div
                  key={i}
                  className={`summary-holder ${
                    item.productProtect ? "two" : ""
                  }`}
                >
                  <div
                    className={`summary-holder__label ${
                      item.productProtect ? "two" : ""
                    }`}
                  >
                    <Icon iconKey={item.title.toLowerCase()}></Icon>
                    <span className="capital">{item.title}</span>
                    <div className="value">{item.sub}</div>
                  </div>
                  <div className="summary-holder__product">
                    {item.productProtect && (
                      <div className="two-lines">
                        <span className="horizontal"></span>
                        <span className="vertical"></span>
                      </div>
                    )}
                    {item.productProtect ? (
                      item.productProtect.map((ls, i) => {
                        return (
                          <PairContainerGroup
                            key={i}
                            index={i}
                            title={ls.productTitle}
                            items={ls.description}
                          />
                        );
                      })
                    ) : (
                      <PairContainerGroup
                        index={""}
                        title={item.productTitle}
                        items={item.description }
                      />
                    )}
                  </div>
                </div>
              );
            })}

            <span className="vertical-connection"></span>
          </div>
          <div className="summary-holder__lens">
            <span className="horizontal-connection"></span>
            <span className="horizontal-dotted"></span>
            <Icon iconKey="lensTwo" />
          </div>
        </div>
        <div className="thickness-holder">
          <Icon iconKey="summary-lens-thickness"></Icon>
          Lens thickness
          <div className="value">1,5</div>
        </div>
      </div>
    );
}
 
export default SummaryContainer;