import React, { useState, useContext } from 'react';
import Button from '../../components/Button';
import SwitchComponent from "../../components/SwitchComponent";
import { AppContext } from '../../context/AppContext';
import { need_explorer } from "../../constants/need-explorer";

const General = () => {
    const { appState, setAppState } = useContext(AppContext);
    const { needExplorerController } = appState;
    const { progress } = needExplorerController;

    const [ state, setState ] = useState({
        questionIndex: 0,
        questions: [
            {
                question: "Gender nisi porta ultrices placerat?",
                component: need_explorer.BASIC_RADIO_BUTTON
            },
            {
                question: "Nisi porta gender porta ultrices placerat?",
                component: need_explorer.RADIO_BUTTON_BELOW
            },
        ]
    })
    
    const { questions, questionIndex} = state;
    

    const handleBack = () => {
        setAppState({
          ...appState,
          needExplorerController: {
            ...needExplorerController,
            progress: progress - 10,
          },
        });
        setState({
            ...state,
            questionIndex: questionIndex-1
        })
    }

    const handleNext = () => {
        if(questionIndex === questions.length-1){
            setAppState({
                ...appState,
                needExplorerController: {
                    ...needExplorerController,
                    activeCategory: 'screenUsage',
                    progress: progress+10
                }
            })
        }else{
            setAppState({
              ...appState,
              needExplorerController: {
                ...needExplorerController,
                progress: progress+10,
              },
            });
            setState({
                ...state,
                questionIndex: state.questionIndex+1
            });
        }
    }

    const renderBody = () => {
      const { question, subQuestion, component } = questions[questionIndex];
      return (
        <div className="question-container">
          <h1 className="question">{question}</h1>
          {subQuestion && <p className="sub-question">{subQuestion}</p>}
          {/* <SwitchComponent questionIndex={questionIndex} /> */}
          <SwitchComponent component={component} />
        </div>
      );
    };

    return (
        <div className="general">
            <div className="general__progress">
                { questionIndex !== 0 &&
                <Button
                    icon="start-arrow"
                    className="icon-btn"
                    handler={handleBack}
                    ></Button> }
                <div className="step">
                    {`${questionIndex+1} / ${questions.length}`}
                </div>
            </div>
            <div className="general__body">
                {renderBody()}
            </div>
            <div className="general__nextButton">
                <Button
                    className="next-btn compact"
                    text="Next"
                    icon="arrow-white-blue"
                    handler={handleNext}
                ></Button>
            </div>
        </div>
    )
}

export default General;