import React, { useState } from 'react';
import { useHistory } from 'react-router';
import Button from '../../components/Button';
import routes from '../../constants/routes';
import eyeImage from "../../assets/images/eye.png";

const PackageOffer = () =>  {
    // VARIABLES
    const history = useHistory();
    const [selectedPackage, setSelectedPackage] = useState()
    const packages = [
        {
            label: 'Personalized',
            id: 'personalized',
            product: {
                price: '321,00 ',
                name: 'Varilux Physio 3.0 FIT'
            }
        },
        {
            label: 'Optimized',
            id: 'optimized',
            product: {
                price: '294,00 ',
                name: 'Varilux Physio 3.0 FIT'
            }

        },
        {
            label: 'Standard',
            id: 'standard',
            product: {
                price: '258,00 ',
                name: 'Varilux Comfort 3.0 '
            }
        }
    ]

    // HOOKS

    // METHODS
    const back = () => {
        history.push(routes.LPB.path);
    }
    const selectedPackageClassname = (ppackage) => {
        return selectedPackage && ppackage.id === selectedPackage.id ? 'package-offer__card--selected' : '';
    }

    return (
        <div className="package-offer">
            <div className="package-offer__buttons">
                <Button
                    className="basic-icon-btn back-btn"
                    text="Back"
                    icon="start-arrow"
                    handler={back}
                ></Button>
                <Button
                    className="next-btn compact pid-btn"
                    text="Save"
                    icon="arrow-white-blue"
                ></Button>
            </div>


            <div className="package-offer__cards">
                {
                    packages.map((item, index) => (
                        <div 
                            onClick={() => setSelectedPackage(item)}
                            className={`package-offer__card package-offer__card--${item.id} ${selectedPackageClassname(item)}`} 
                            key={index}>

                            <div className="package-offer__card-info">
                                <label>{item.label}</label>

                                <div>
                                    <img src={eyeImage} alt=""/>
                                </div>
                            </div>


                            <div className="package-offer__card-details">
                                <div className="package-offer__card-product">
                                    <span>{item.product.price}</span>
                                    <h2>{item.product.name}</h2>
                                </div>

                                <ul className="package-offer__card-lenses">
                                    <li>
                                        <span></span>
                                        <label>Varilux Xclusive 4D</label>
                                    </li>
                                    <li>
                                        <span></span>
                                        <label>Transitions</label>
                                    </li>
                                    <li>
                                        <span></span>
                                        <label>Crizal Easy UV</label>
                                    </li>
                                </ul>
                            </div>

                            

                        </div>
                    ))
                }
            </div>
        </div>
    )
}

export default PackageOffer;