import Slider from "react-input-slider";
import React, { useState } from "react";
import Icon from "../components/Icon";

const ColorSlider = ({ sendColor, onSelectOpacity }) => {
  const [state, setState] = useState({ y: 50 });
  const { y } = state;

  const handleOpacity = (y) =>{
    setState({ y: parseFloat(y.toFixed(2)) })
    onSelectOpacity(y);
  }

  return (
    <div className="slider">
      <Icon iconKey="sun-icon" />
      <Slider
        styles={{
          active: {
            backgroundColor: "CCE7F7",
          },
        }}
        axis="y"
        ystep={1}
        ymin={90}
        ymax={10}
        y={y}
        onChange={({ y }) => {handleOpacity(y)}}
      />
      <Icon iconKey="dark-icon" />
    </div>
  );
};
 
export default ColorSlider;