import React from 'react';

const DragAndDropSorter = ({
    items
}) => {

    const renderNumberedBoxes = () => {
        const boxes = items.map((i,j)=>{
            return (
                <div className="numbered-box" key={j}>
                    {j+1}
                </div>
            )
        })

        return boxes;
    }

    const renderTextBoxes = () => {
        const boxes = items.map((i,j)=>{
            return (
                <div className="text-box" key={j}>
                    {i.text}
                </div>
            )
        })

        return boxes;
    }

    return (
        <div className="dnd">
            <div className="dnd__numbered-boxes">
                {renderNumberedBoxes()}
            </div>
            <div className="dnd__text-boxes">
                {renderTextBoxes()}
            </div>
        </div>
    )
}

export default DragAndDropSorter;