import React from 'react';
import ReactHtmlParser from 'react-html-parser'

const LegalNotices = () => {
    const sections = [
        {
            header: "Demonstrations",
            content: "<p>Please note that the information presented in this app is for demonstration purposes only. Comparison of products which might be quoted within the app, are for illustration only and must not be interpreted as comparative advertising.</p><p>Each Essilor product, brand and or technology (Crizal...) does own specific marketing claims, to which you should revert, while promoting the benefits and properties of the said product.</p>"
        },
        {
            header: "Data",
            content: "<p>By choosing to use the Essilor Companion Progressive web app you agree to accept the terms and conditions of this Legal Notice governing use of the solution.</p><p>Why does Essilor use your data? To give you a customized experience of the solution, understand how users use Essilor Companion and improve our features.</p><p>Here is a list of data Essilor Companion collects, stores and uses:<span>-Monthly usage time: hours and minutes of the licence</span><span>-Demonstration click count</span><span>-Features click count including eMat Lens Proposal Builder and Whats New</span></p><p>The data recipients are Essilor and selected third parties</p>"
        },
        {
            header: "Your rights",
            content: "<p>Following legislation your rights includes:<span>-The right to access, rectify or erase this data. Kindly ask your Essilor representative to follow your request.</span><span>-The right to stop Essilor Companion collecting data by turning off internet connection before usage</span><p>"
        }
    ]

    const renderSections = () => {
        const sections_jsx = sections.map((section, j)=>{
            return (
                <div className="section" key={j}>
                    <div className="number-bullet">{`0${j+1}`}</div>
                    <div className="section-content">
                        <h2 className="section-content__header">{section.header}</h2>
                        <div className="section-content__paragraph">
                            {ReactHtmlParser(section.content)}
                        </div>
                    </div>
                </div>
            )
        })
        
        return sections_jsx
    }

    return (
        <div className="legal-notices">
            {renderSections()}
        </div>
    )
}

export default LegalNotices;