import React, { useContext } from 'react';
import MessageBox from "../components/MessageBox";
import { AppContext } from '../context/AppContext';

const MessageGroup = ({
    items
}) => {
    const { appState, setAppState } = useContext(AppContext);
    const { needExplorerController } = appState;

    const handleClick = () => {
        setAppState({
            ...appState,
            showModalDim: true,
            needExplorerController: {
                ...needExplorerController,
                isSummaryModalShow: true
            }
        })
    }
    
    return ( 
        items.map((value,i)=>{
            return(
                <MessageBox 
                    key={i}
                    text={value.text}
                    subtext={value.labelText}
                    handler={handleClick}
                />
            )
        }) 
    );
       
}
 
export default MessageGroup;