
import React, { useContext } from "react";
import Button from "../components/Button";
import Icon from "../components/Icon";
import RadioButtonPrescription from "../components/RadioButtonPrescription";
import ProgressCircle from "../components/ProgressCircle";
import Precision from "../components/Precision";
import Modal from "../components/Modal";
import { AppContext } from "../context/AppContext";
import TitleHeader from "../components/TitleHeader";
import { useHistory } from "react-router-dom"

const Prescription = () => {
  const history = useHistory();
  const { appState, setAppState } = useContext(AppContext);
  const { prescriptionController, showModalDim } = appState;
  const { active, isNext, isImportModalShow } = prescriptionController;

  const handlePrecisionClick = (precision) => {
    setAppState({
      ...appState,
      prescriptionController: {
          ...prescriptionController,
          active: precision
      }
    })
  }

  const handleNextButton = () => {
    setAppState({
      ...appState,
      prescriptionController: {
        ...prescriptionController,
        isNext: true
      }
    })
  }

  const handleBackButton = () => {
    setAppState({
      ...appState,
      prescriptionController: {
        ...prescriptionController,
        isNext: false
      }
    })
  }

  const handleSaveAndNextButton = () => {
    setAppState({
      ...appState,
      prescriptionController: {
        ...prescriptionController,
        isNext: false
      }
    })

    history.push("/lens-proposal-builder");
  }

  const handleImport = () => {
    setAppState({
        ...appState,
        showModalDim: !showModalDim,
        prescriptionController:{
            ...prescriptionController,
            isImportModalShow: !isImportModalShow
        }
    })
  }

  const renderNavigators = () => {
    return (
      <React.Fragment>
        <div className="navigator">
          <div className="container">
            <div className={`container__btn ${active === "standard" ? 'active' : ''}`}
                onClick={() => handlePrecisionClick('standard')}
            >
              Standard Precision
            </div>
            <div className={`container__btn ${active === "ava" ? 'active' : ''}`}
                onClick={() => handlePrecisionClick('ava')}
            >
              AVA Precision
            </div>
          </div>
        </div>
        <div className="sca">
          <Precision
            title="Sphere (SPH)"
            odValue="+4,27"
            osValue="+1,52"
          />
          <Precision
            title="Cylinder (CYL)"
            odValue="+3,22"
            osValue="+2,51"
          />
          <Precision
            title="Axis (AXE)"
            odValue="+0,24"
            osValue="+2,28"
          />
        </div>
        <div className="add">
          <div className="add__title">
            Addition (ADD)
          </div>
          <div className="add__tbox">
            +0,28
          </div>
        </div>
      </React.Fragment>
    )
  }

  const renderLensChoices = () => {
    return (
      <div className="lens-choices">
         <Button
          className=" back-btn"
          text={'Back'}
          icon="start-arrow"
          handler={handleBackButton}
        ></Button>
        <div className="scroll">
          <TitleHeader headerText="GENERAL EQUIPMENT"/>
          <RadioButtonPrescription
            titleHeader="Correct"
            choices={[
              {text: "Progressive"},
              {text: "Single Vision"},
              {text: "Personalized"},
            ]}
          />
          <RadioButtonPrescription
            titleHeader="Protect"
            choices={[
              {text: "Blue cut"},
              {text: "Photochromic"},
              {text: "Tinted lens"},
            ]}
          />
          <RadioButtonPrescription
            titleHeader="Enhance"
            choices={[
              {text: "AR Lens"}
            ]}
          />
          <TitleHeader headerText="COMPLEMENTARY EQUIPMENT"/>
          <RadioButtonPrescription
            titleHeader=""
            choices={[
              {text: "Driving"},
              {text: "Computer"},
              {text: "Sun"}
            ]}
          />
        </div>
      </div>
    )
  }

  const renderModalImportContent = () => {
    return (
        <div className="prescription-import-modal-content">
            <div className="btn-header">
                <Button
                    className="icon-btn close"
                    icon="close-button"
                    handler={handleImport}
                ></Button>
            </div>
            <h2 className="prescription-import-modal-header">Import</h2>
            <div className="panel-import-buttons">
                <Button
                    className="basic-icon-btn white simple"
                    text="QR Code"
                    icon="qr-code"
                ></Button>
                <Button
                    className="basic-icon-btn white simple"
                    text="ID"
                    icon="id"
                    handler={()=>{}}
                ></Button>
            </div>
        </div>
    )
  }

  return (
    <div className="prescription">
      <div className="prescription__left">
        <div className="container">
          <div className="border"></div>
          <div className="content">
            <div className="content__header">
              <ProgressCircle size="22" progress={20} stroke="2" />
              <h4 className="header-text">{isNext ? 'Category Lens Proposal' : 'Prescription'}</h4>
            </div>
            <div className="content__image">
              {
                isNext ? (
                  <Icon
                  iconKey={`lensBaseSide`}
                  ></Icon>
                ) : (
                  <Icon
                  iconKey={`${active === "standard" ? "eye-reader" : "eye-reader2"}`}
                  ></Icon>
                )
              }
            </div>
            <div className="content__buttons">
              <div className="btn-grp">
                <Button 
                  className="basic-icon-btn simple compact"
                  icon="icon-import"
                  text=""
                  handler={handleImport}
                ></Button>
                <Button
                  className="next-btn compact"
                  text={`${isNext ? 'Save & Next' : 'Next'}`}
                  icon="arrow-white-blue"
                  handler={isNext ? handleSaveAndNextButton : handleNextButton}
                ></Button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="prescription__right">
        {isNext ? renderLensChoices() : renderNavigators()}
      </div>
      <Modal className="modal-simple measures-modal-content" isShow={isImportModalShow} content={renderModalImportContent()} /> 
    </div>
  );
}
 
export default Prescription;