import React, { useContext, useEffect, useState } from 'react';
import DropDown from '../../../components/ui/dropdown/DropDown';
import ToggleButton from '../../../components/ui/toggleButton/ToggleButton';
import ToggleRadio from '../../../components/ui/toggleRadio/ToggleRadio';
import Slider from 'react-input-slider';
import { AppContext } from '../../../context/AppContext';


const EducationControls = ({
    view = 'expanded',// 'collapsed'
}) =>  {
    // VARIABLES
    const B = 'education-controls';
    const [item, setItem] = useState('off');
    const [lens, setLens] = useState('off');
    const [lensSlider, setLensSlider] = useState(5);
    const [visualDefect1, setVisualDefect1] = useState();
    const [visualDefect2, setVisualDefect2] = useState();
    const { appState, setAppState } = useContext(AppContext);
    
    const controlOptions = {
        visualDefect1: [
            { label: 'Myopia', value: 'myopia' },
            { label: 'Hyperopia', value: 'hyperopia' },
            { label: 'Astigmatism', value: 'astigmatism' },
            { label: 'Presbyopia', value: 'presbyopia' },
        ],
        visualDefect2: {
            myopia: [
                { label: 'Astigmatism', value: 'astigmatism' },
                { label: 'Presbyopia', value: 'presbyopia' },
            ],
            hyperopia: [
                { label: 'Astigmatism', value: 'astigmatism' },
                { label: 'Presbyopia', value: 'presbyopia' },
            ],
            presbyopia: [
                { label: 'Myopia', value: 'myopia' },
                { label: 'Hyperopia', value: 'hyperopia' },
                { label: 'Astigmatism', value: 'astigmatism' },
            ],
            astigmatism: [
                { label: 'Presbyopia', value: 'presbyopia' },
            ],
            none:[]
        },
        item: [
            { label: 'Standard', value: 'standard' },
            { label: 'Car', value: 'car' },
            { label: 'Flower', value: 'flower' },
        ],
        environment: [
            { label:'Sunlight', value: 'sunlight' }, 
            {label:'Night', value: 'night'}
        ]
    }

    const sliderStyle = {
        track: {
            backgroundColor: "#CFD3D7"
        },
        active: {
            backgroundColor: "transparent",
            borderRadius: "1em"
        },
        thumb: {
            width: 20,
            height: 20,
            opacity: 1
        }
    }

    // HOOKS
    useEffect(() => {
        itemOnSelect(appState.education.item);
    }, [appState.education.item])

    useEffect(() => {
        lensOnSelect(appState.education.lens);
    }, [appState.education.lens])

    // METHODS
    const E = (el, mod) => {
        const element = `${B}__${el}`;
        let modifier = '';
        if(mod) {
            modifier = mod.map( x => `${element}--${x}`).join(" ");
        }
        return `${element} ${modifier}`;
    }

    const getVisualDefect2 = () => {
        return visualDefect1 ? 
                controlOptions.visualDefect2[visualDefect1.value] : 
                controlOptions.visualDefect2.none;
    }

    const onVisualDefectChanged = (option) => {
        setVisualDefect1(option);
        setVisualDefect2(null);
        setAppState({
            ...appState,
            education: {
                ...appState.education,
                visualDefect1: option.value
            }
        })
    }

    const onEnvironmentChanged = (value) => {
        setAppState({
            ...appState,
            education: {
                ...appState.education,
                environment: value
            }
        })
    }

    const itemOnSelect = (value) => {
        setItem(value);
        setAppState({
            ...appState,
            education: {
                ...appState.education,
                item: value
            }
        })
    }

    const lensOnSelect = (value) => {
        setLens(value);
        setAppState({
            ...appState,
            education: {
                ...appState.education,
                lens: value
            }
        })
    }

    return (
        <div className={`${B} ${B}--${view}`}>
            
            <div className={E('control')}>
                <label>Visual defect 1</label>

                <DropDown 
                    onSelect={opt => onVisualDefectChanged(opt)}
                    options={controlOptions.visualDefect1}/>
            </div>
            <div className={E('control')}>
                <label>Visual defect 2</label>

                <DropDown 
                    onSelect={(v) => setVisualDefect2(v)}
                    defaultValue={{value:'', label:'None'}}
                    value={visualDefect2}
                    options={getVisualDefect2()}/>
            </div>
            <div className={E('control')}>
                <label className={item === 'off' ? 'temp-no-padding-bottom' : ''}>
                    Item <ToggleRadio value={item} onChange={(v) => itemOnSelect(v)}/>
                </label>
                
                {
                    item === 'on' &&
                    <DropDown 
                        defaultValue={controlOptions.item[0]}
                        options={controlOptions.item}/>
                }

            </div>
            <div className={E('control')}>
                <label className={lens === 'off' ? 'temp-no-padding-bottom' : ''}>
                    Lens <ToggleRadio value={lens} onChange={(v) => lensOnSelect(v)}/>
                </label>
                {
                    lens === 'on' && 
                    <div className={E('control-lens-slider')}>
                        <label>Puissance correction</label>
                        <div>
                            <span>-10</span>
                            <Slider
                                className="slider"
                                axis="x"
                                xstep={1}
                                xmin={0}
                                xmax={10}
                                x={lensSlider}
                                styles={sliderStyle}
                                onChange={({x}) => setLensSlider(x)}
                            />
                            <span>+10</span>
                        </div>
                    </div>    
                }

            </div>
            <div className={E('control')}>
                <label>Environment</label>

                <ToggleButton 
                    onChange={ v => onEnvironmentChanged(v) }
                    options={controlOptions.environment}/>
            </div>
            <div className={E('control')}>
                <label>Screen mode</label>

                <ToggleButton options={[{label:'Simplify', value: '1'}, {label:'Environment', value: '2'}]}/>
            </div>

        </div>
    )
}

export default EducationControls;