import React, {
    useState,
    useEffect
} from 'react';
import PictureButton from "./PictureButton";

const PictureButtonGroup = ({
        items
    }) => {
        const [state, setState] = useState();

        useEffect(() => {
            const item = items.map(i => {
                return {selected: i.selected}
            })
    
            setState(item)
        }, [items]);
        const selectComponent = (index) => {
            state[index] ={
                selected: !state[index].selected
            };
            setState({
                ...state,
            });            
        }
    const renderItems = () =>{
        const item =  (
            items.map((value,i)=>{
                return(<PictureButton
                    key={i}
                    text={value.text}
                    index={value.type}
                    selected={state[i].selected}
                    handle={selectComponent}
                    parameter={i}
                  />)
            })
        );
        return item;
    }
    return (
        <div className="answers__grid--four">
            {state ? renderItems() : ''}
        </div>
    )
}

export default PictureButtonGroup