import React, { useContext, useState} from 'react';
import { AppContext } from "../context/AppContext";
import Modal from "../components/Modal";
import Button from "../components/Button";

const NEModalContainer = () => {
    const { appState, setAppState } = useContext(AppContext);
    const { needExplorerController } = appState

    const [state, setLocalState] = useState({
        isModalNext: false,
        content1: {
          stepperProgress: "1/2",
          title: "Personalize your lenses",
          subTitle: "Essilor brings the best technologies together to create the perfect lens for your lifestyle. Tell us about your needs so we can help you find a customised solution for your best vision possible."
        },
        content2: {
          stepperProgress: "2/2",
          title: "Legal notices",
          subTitle: "The purpose of the questionaire is to provide you with recommendations based on persnal data, vision challenges and hobbies. These data are not stored. They ar used to give a personalised recommendation."
        }
    })

    const { isModalNext } = state

    const handleNextBack = () => {
        setLocalState({
          ...state,
          isModalNext: !isModalNext
        })
    }

    const handleAccept = () => {
        setAppState({
            ...appState,
            needExplorerController: {
                ...needExplorerController,
                pageProgress: 'questionnaire-page'
            }
        })
    }
    
    const StepContent = () => {
        const { content1, content2 } = state
        const content = isModalNext ? content2 : content1
        const { stepperProgress, title, subTitle } = content
    
        return (
          <div className="content">
            <div className="content__header">
              { isModalNext &&
              <Button
                icon="start-arrow"
                className="icon-btn"
                handler={handleNextBack}
                ></Button> }
              <div className="step">
                {stepperProgress}
              </div>
            </div>
            <div className="content__body">
              <div className="title">
                {title}
              </div>
              <div className="subtitle">
                {subTitle}
              </div>
            </div>
            <div className="content__footer">
              <div className="btn-grp">
                { isModalNext &&
                  <Button
                    className="basic-icon-btn compact white-bg bordered"
                    text="Decline"
                    icon="arrow-white-blue"
                ></Button> }
                <Button
                  className="next-btn compact"
                  text={`${!isModalNext ? 'Next' : 'Accept'}`}
                  icon="arrow-white-blue"
                  handler={!isModalNext ? handleNextBack : handleAccept}
                ></Button>
              </div>
            </div>
          </div>
        )
    }

    return (
        <div className="need-explorer__modal-page">
            <Modal 
                isShow={true}
                content={StepContent()}
            />
        </div>
    )
}

export default NEModalContainer;