import React from "react";
import Icon from "./Icon";

const Button = ({
  text,
  icon,
  className,
  handler,
  value,
  iconAround
}) => {
  return (
    <button
      className={className}
      onClick={handler ? () => handler(value) : () => {}}
    >
      {className !== "basic-btn" && !iconAround && <Icon iconKey={icon}></Icon>}
      {className.includes("exp-btn") && <span className="circle"></span>}
      {className !== "icon-btn" && text && <h2 className="text">{text}</h2>}
      {className === "companion-btn active" && (
        <span className="right">
          <Icon iconKey="companion-right-arrow"></Icon>
        </span>
      )}
      {iconAround && (
        <div className="icon-border">
          <div className="img">
            <Icon iconKey={icon}></Icon>
          </div>
        </div>
      )}
    </button>
  );
};

export default Button;
