const PictureButton = ({selected, index, text, handle, parameter}) => {
    return (
      <div
        className={`checklist__picture ${index} ${selected ? "selected" : ""}`}
        onClick={() => (handle ? handle(parameter) : "")}
      >
        <div className="text">{text}</div>
        <div className="checkboxContainer">
          <label className="checkbox">
            <input
              type="checkbox"
              name="transportAns"
              checked={selected}
              onChange={(e) => {}}
            />
            <span className="checkmark"></span>
          </label>
        </div>
      </div>
    );
}
 
export default PictureButton;