import React, { useContext, useState } from "react";
import LPBButtons from "../components/LPBButtons";
import Icon from '../components/Icon';
import { routes } from "../constants/routes"; 
import { AppContext } from "../context/AppContext";
import Modal from '../components/Modal';
import Button from '../components/Button';
import TextBox from '../components/TextBox';

const LensProposalBuilder = () => {
  const { appState, setAppState } = useContext(AppContext);
  const { id, showModalDim, lpbController } = appState;
  const { modalShow, invalidPopup } = lpbController;
  const [state, setState] = useState({ buttonId: 0, toggle: false});
  const {toggle, buttonId} = state;
    const theme = ["Standard Experience", "AVA Experience", "Sun Experience"];

    const seletedTheme = (value) =>{
      setState({
        ...state,
        buttonId: value
      });
    }
    const displayTheme = () =>{
      setState({
        ...state,
        toggle: !toggle,
      });
    }

  const renderModalContent = () => {
    return (
      <div className="modal-content">
        <div className="btn-header">
            <Button
                className="icon-btn close"
                icon="close-button"
                handler={handleEditModal}
            ></Button>
        </div>
        <h2 className="lpb-modal-header">Modify ID</h2>
        <div className="text-btn-grp">
          <TextBox
            value={id}
            className="border-blue lpb-id-tbox"
            handler={handleIDTextBox}
            placeholder="Enter ID"
            max={6}
          />
          <Button
            className="next-btn compact"
            text="Next"
            icon="arrow-white-blue"
            handler={handleEdit}
          ></Button>
        </div>
      </div>
    )
  }

  const renderModalInvalid = () =>{
    return(
      <div className="modal-content">
      <div className="btn-header">
            <Button
                className="icon-btn close"
                icon="close-button"
                handler={handleInvalidShow}
            ></Button>
        </div>
        <h2 className="lpb-modal-header invalid">You have entered an invalid  or not existing ID.<br/><br/> Please Try Again</h2>
      </div>
    )
    
  }

  const handleInvalidShow = () => {
    setAppState({
      ...appState,
      showModalDim: !showModalDim,
      lpbController: {
        ...lpbController,
        invalidPopup: !invalidPopup
      }
    })
  }

  const handleEditModal = () => {
    setAppState({
      ...appState,
      showModalDim: !showModalDim,
      lpbController: {
        ...lpbController,
        modalShow: !modalShow
      }
    })
  }

  const handleIDTextBox = (e) => {
    const { value } = e.target
    
    setAppState({
      ...appState,
      id: value
    })
  }

  const handleEdit = () => {
    if(parseInt(id) === 0){
      setAppState({
        ...appState,
        lpbController: {
          ...lpbController,
          modalShow: !modalShow,
          invalidPopup: !invalidPopup
        }
      })
    }else{
      setAppState({
        ...appState,
        showModalDim: !showModalDim,
        lpbController: {
          ...lpbController,
          modalShow: !modalShow
        }
      });
    }    
  }

  return (
    <div className="lensproposal">
      <div className={`lensproposal__up ${toggle ? "close" : ""}`}>
        <div className="overlay"></div>
        <div className="buttonsGrp">
          <LPBButtons
            title={"Need Explorer"}
            statusText={"Completed"}
            selector={"blue"}
            displayExtra={"display"}
            icon={"modifyIcon"}
            displayFooter={"hide"}
            route={routes.NEED_EXPLORER.path}
          />
          <LPBButtons
            title={"Prescription"}
            statusText={"Completed"}
            selector={"blue"}
            displayExtra={"display"}
            icon={"modifyIcon"}
            displayFooter={"hide"}
            route={routes.PRESCRIPTION.path}
          />
          <br />
          <LPBButtons
            title={"Measures"}
            statusText={"Completed"}
            selector={"white"}
            displayExtra={"hide"}
            icon={"addIcon"}
            displayFooter={"hide"}
            route={routes.MEASURES.path}
          />
          <LPBButtons
            title={"Lens Consultation"}
            statusText={"Completed"}
            selector={"white"}
            displayExtra={"hide"}
            icon={"addIcon"}
            displayFooter={"display"}
            route={routes.LENS_CONSULTATION.path}
          />
          <LPBButtons
            title={"Delivery"}
            statusText={"Completed"}
            selector={"white"}
            displayExtra={"hide"}
            icon={"addIcon"}
            displayFooter={"hide"}   
            route={routes.DELIVERY.path}         
          />
        </div>

        <div className="lens-footer">
          <div className="lens-footer__id">
            <strong>{id}</strong>
            <div className="icon" onClick={handleEditModal}>
              <Icon iconKey="btnModify" />
            </div>
          </div>
          <div className="lens-footer__exp">
            <strong>{toggle ? "Fermer" : "Experiences"}</strong>
            {toggle ? (
              <Button
                className="icon-btn close"
                icon="close-button"
                handler={displayTheme}
              ></Button>
            ) : (
              <div className="icon" onClick={displayTheme}>
                <Icon iconKey="btnExp" />
              </div>
            )}
          </div>
        </div>
      </div>
      <div className={`lensproposal__experience ${toggle ? "open" : ""}`}>
        {theme.map((item, i) => {
          return (
            <Button
              key={i}
              className={`exp-btn ${buttonId === i ? "selected" : ""}`}
              text={item}
              handler={seletedTheme}
              value={i}
            ></Button>
          );
        })}
      </div>
      <Modal
        className="modal-simple lpb-modal-content"
        isShow={modalShow}
        content={renderModalContent()}
      />
      <Modal
        className="modal-simple lpb-modal-content"
        isShow={invalidPopup}
        content={renderModalInvalid()}
      />
    </div>
  );
}
 
export default LensProposalBuilder;