import React, { useContext } from "react";
import ColorSlider from "../components/ColorSlider";
import Button from "../components/Button";
import Icon from "../components/Icon";
import ProgressCircle from "../components/ProgressCircle";
import { AppContext } from "../context/AppContext";
import { Link} from "react-router-dom";

const LensLeftContainer = ({ displayOption, displaySlider, demobBtn, route }) => {
    const { appState } = useContext(AppContext);
    const { stepProgress } = appState;
    const { activeLabel, progress } = stepProgress;

    const handleOpacity = (value) =>{
      const overlay = document.getElementById("product-shape");
      overlay.style.fill = "rgba(0,0,0,0." + value +")";
    }
    const lensDisplay = () =>{
      if(!displaySlider){
        switch (activeLabel) {
          case "Prescription": {
            return (
              <div className="prescription-display">
                <Icon iconKey="lensOne" />
                <Icon iconKey="lensTwo" />
                <Icon iconKey="lensThree-display" />
              </div>
            );
          }

          case "correct": {
            return (
              <div className="correct-display">
                <Icon iconKey="lensThree-display" />
                <Icon iconKey="lens-line" />
                <Icon iconKey="lens-line-two" />
              </div>
            );
          }
          case "protect": {
            return (
              <div className="protect-display">
                <Icon iconKey="lens-line" />
                <Icon iconKey="lensThree-display" />
                <Icon iconKey="lens-line-two" />
              </div>
            );
          }
          case "enhance": {
            return (
              <div className="enhance-display">
                <Icon iconKey="lensThree-display" />
                <Icon iconKey="lens-line" />
                <Icon iconKey="lens-line-two" />
              </div>
            );
          }

          default:
            return ""
        }
      }
    }
    const displayImage = () =>{
      if (demobBtn){
        return (
          <div>
            {lensDisplay()}
            {displaySlider && (
              <div className="transition-container">
                <Icon iconKey="lensOne" />
                <Icon iconKey="lensTwo" />
                <Icon iconKey="lensThree" />
                <Icon iconKey="lensThree-img" />
              </div>
            )}
            {displaySlider && <ColorSlider onSelectOpacity={handleOpacity} />}
          </div>
        );
      }
      else{
        return(<Icon iconKey="lens-thickness"></Icon>)
      }
    }

    return (
      <div className="left-container__left">
        <div className="border"></div>
        {/* Header (progress, label) */}
        <div className="left-container__left--header">
          <ProgressCircle size="22" progress={progress} stroke="2" />
          <strong>{progress >= 100 ? "Completed" : activeLabel}</strong>
          <div className="icons">
            <Icon iconKey="pairOne" />
            <span className="vl"></span>
            <Icon iconKey="pairTwo" />
          </div>
        </div>
        <div className="left-container__left--demo">{displayImage()}</div>
        {/* Buttons below */}
        {displayOption && (
          <div className="left-container__left--options">
            {demobBtn && (
              <Button
                className="basic-icon-btn demo-btn"
                text="Demo"
                icon="eye-icon"
              ></Button>
            )}
            <Link to={route}>
              <Button
                className="next-btn"
                text="Save & Next"
                icon="arrow-white-blue"
                route
              ></Button>
            </Link>
          </div>
        )}
      </div>
    );
    };

export default LensLeftContainer;