import Icon from "../components/Icon";

const ProgressCircle = ({size, stroke, progress}) => {
    const progSize = progress > 100 ? 100 : progress < 0 ? 0 : progress;
    const viewBox = `0 0 ${size} ${size}`;
    const radius = (size - stroke) / 2;
    const dashArray = radius * Math.PI * 2;
    const dashOffset = dashArray - (dashArray * progSize) / 100;
    return (
      <div>
        <svg width={size} height={size} viewBox={viewBox}>
          <circle
            className="circle-background"
            cx={size / 2}
            cy={size / 2}
            r={radius}
            strokeWidth={`${stroke}px`}
          />
          <circle
            className="circle-progress"
            cx={size / 2}
            cy={size / 2}
            r={radius}
            strokeWidth={`${stroke}px`}
            transform={`rotate(-90 ${size / 2} ${size / 2})`}
            style={{
              strokeDasharray: dashArray,
              strokeDashoffset: dashOffset,
            }}
          />
          {progSize >= 100 && <Icon iconKey="check-prog" />}
        </svg>
      </div>
    );
}
 
export default ProgressCircle;