import React, { useContext } from "react";
import Icon from "../components/Icon";
import { AppContext } from "../context/AppContext";
import { Link } from 'react-router-dom';

const LPBButtons = ({
  title,
  statusText,
  displayFooter,
  selector,
  displayExtra,
  icon,
  route,
  handler
  
}) => {
  const { appState, setAppState } = useContext(AppContext);
  const { stepProgress } = appState;
  const { progress } = stepProgress;
  
  
  const handleClick = (value) =>{
    if(handler){
      return handler(value);
    }
    else if(value){
      return setAppState({
        ...appState,
        stepProgress: {
          ...stepProgress,
          activeLabel: value.toLowerCase(),
          progress: progress + 33,
        },
      });
    }
    else{
      return "";
    }
  }
  return (
    <div className="button">
      <Link to={route || '#'} style={{ textDecoration: 'none' }}>
      <div className={`button__${selector}`}>
        <div className="content center">
          <div className="content-left">
            <h2>{title}</h2>
            <div className={`content-left-status ${displayExtra}`}>
              <span className="dot"></span>
              {statusText}
            </div>
          </div>
          <div className="content-icon" onClick={()=>handleClick(title)}>
            <div className="img">
              <Icon iconKey={icon} />
            </div>
          </div>
        </div>
        <div className={`footer ${displayFooter}`}>
          <strong>Impacted by:</strong>
          <span className="subtopic">Need Explorer</span>
          <span className="subtopic">Prescription</span>
        </div>
      </div>
      </Link>
    </div>
  );
};
 
export default LPBButtons;