import { routes } from "../constants/routes";
import { useLocation } from "react-router-dom";
import { AppContext } from "../context/AppContext";
import React, { useContext } from "react";

const SelectionButton = ({title, handler, parameter, borderDisplay, type}) => {
  const { appState } = useContext(AppContext);
  const { myCompanionController} = appState;
  const {
    selectedCategory, showPrice
  } = myCompanionController;
  const location = useLocation();
    return (
      <div className="selection__list--card sub">
        <div
          className={`selection__list--content--sub  ${
            selectedCategory === "lensThickness" ? "companion" : ""
          }`}
        >
          <div className="vl"></div>
          <label
            className={`radio ${
              selectedCategory === "lensThickness" ? "companion" : ""
            }`}
          >
            <input
              type={type}
              name={parameter}
              onChange={() => (handler ? handler(parameter, title) : "")}
            />
            {borderDisplay && <div className="border display"></div>}
            <span className="selection__text">{title}</span>
            {location.pathname === routes.MY_COMPANION.path &&
              showPrice &&
              (selectedCategory === "correct" ||
                selectedCategory === "protect" ||
                selectedCategory === "enhance") && (
                <span className="price-view sub">100 </span>
              )}
            {(location.pathname !== routes.MY_COMPANION.path ||
              selectedCategory === "lensThickness") && (
                <span
                  className={`selection__checkbox
                  }`}
                ></span>
              )}
          </label>
        </div>
      </div>
    );
}
 
export default SelectionButton;