import React, { useState, useContext } from 'react';
import Button from '../../components/Button';
import { AppContext } from '../../context/AppContext';
import SwitchComponent from "../../components/SwitchComponent";

const OutdoorActivities = () => {
    const { appState, setAppState } = useContext(AppContext);
    const { needExplorerController } = appState;
    const { progress } = needExplorerController;

    
    const [ state, setState ] = useState({
        questionIndex: 0,
        questions: [
            {
                question: "Gender nisi porta ultrices placerat?",
                subQuestion: "Gender nisi porta",
                component: "dial"
            },
            {
                question: "Nisi porta gender porta ultrices placerat?",
                subQuestion: "Nisi porta gender",
                component: "dial"
            },
        ]
    })
    const { questions, questionIndex } = state

    const renderBody = () => {
        const { question, subQuestion, component } = questions[questionIndex]
        return (
            <div className="question-container">
                <h1 className="question">{question}</h1>
                {subQuestion && <p className="sub-question">{subQuestion}</p>}
                <SwitchComponent component={component} />
            </div>
        )
    }

    const handleBack = () => {
        setAppState({
          ...appState,
          needExplorerController: {
            ...needExplorerController,
            progress: progress - 10,
          },
        });
        setState({
            ...state,
            questionIndex: questionIndex-1
        })
    }

    const handleNext = () => {
        if(questionIndex === questions.length-1){
            setAppState({
                ...appState,
                needExplorerController: {
                    ...needExplorerController,
                    pageProgress: 'finished-page',
                    progress: progress + 10,
                }
            })
        }else{
            setAppState({
              ...appState,
              needExplorerController: {
                ...needExplorerController,
                progress: progress + 10,
              },
            });
            setState({
                ...state,
                questionIndex: state.questionIndex+1
            })
        }
    }

    return (
        <div className="outdoor-activities">
            <div className="outdoor-activities__progress">
                { questionIndex !== 0 &&
                <Button
                    icon="start-arrow"
                    className="icon-btn"
                    handler={handleBack}
                    ></Button> }
                <div className="step">
                    {`${questionIndex+1} / ${questions.length}`}
                </div>
            </div>
            <div className="outdoor-activities__body">
                {renderBody()}
            </div>
            <div className="outdoor-activities__nextButton">
                <Button
                    className="next-btn compact"
                    text="Next"
                    icon="arrow-white-blue"
                    handler={handleNext}
                ></Button>
            </div>
        </div>
    )
}

export default OutdoorActivities;