import React, { useContext, useEffect, useState } from 'react';
import { AppContext } from '../../../context/AppContext';

const EducationStep = ({
    steps = []
}) =>  {
    // VARIABLES
    const [step, setStep] = useState(0);
    const { appState } = useContext(AppContext);


    const B = 'education-step';
    const E = (el, mod) => {
        const element = `${B}__${el}`;
        let modifier = '';
        if(mod) {
            modifier = mod.map( x => `${element}--${x}`).join(" ");
        }
        return `${element} ${modifier}`;
    }

    // HOOKS

    // METHODS
    const isFirstStep = () => {
        return step === 0;
    }

    const isLastStep = () => {
        return (steps.length === (step + 1));
    }

    const nextStep = () => {
        if (isLastStep())return;
        setStep(step + 1);
    }

    const prevStep = () => {
        if(isFirstStep())return;
        setStep(step - 1);
    }

    

    return (
        <div className={B + `${appState.education.environment === 'night' ? (' '+B+'--inverted') : ''}`}>            
            <div className={E('arrows')}>
                <span className={`aarrow aarrow--left ${isFirstStep() ? 'aarrow--disabled' : ''}`} onClick={prevStep} />
                <span className={`aarrow aarrow--right ${isLastStep() ? 'aarrow--disabled' : ''}`} onClick={nextStep}/>

            </div>

            <label className={E('step')}>Step {step + 1}:</label>

            <p className={E('instruction')}>{steps[step].instruction}</p>

            <div className={E('guide')}>
                {steps[step].guide && steps[step].guide}
            </div>
        </div>
    )
}

export default EducationStep;