import React, { useState } from 'react';
import RadioButton from "../components/RadioButton";

const RadioButtonGroup = ({
    items,
    type
}) => {
  const [state, setState] = useState({
    isSelected: false,
    answerSelected: 0,
    // answers: [
    //   {
    //     id: 1,
    //     type: "one",
    //     text: "Aenean leo leo, pulvinar sed dui et, luctus efficitur orci.",
    //     selected: false,
    //   },
    //   {
    //     id: 2,
    //     type: "two",
    //     text: "Quisque sollicitudin bibendum enim sed suscipit.",
    //     selected: false,
    //   },
    //   {
    //     id: 3,
    //     type: "three",
    //     text: "Fusce a diam a nisi fermentum aliquet.",
    //     selected: false,
    //   },
    //   {
    //     id: 4,
    //     type: "four",
    //     text: "Phasellus efficitur pretium porta, suspendisse malesuada.",
    //     selected: false,
    //   },
    // ],
  });

  const { answerSelected } = state;

  const selectComponent = (index) => {
    setState({
        ...state,
        answerSelected: index,
      });
    
  };
  
  return (
    <div className={type ==="rect" ? "":"answers__grid"}>
      {/* Dynamically return it here based of number of radio-button items check
      ./SliderGroup for reference */}
      {items.map((value, i)=>{
          return (
            <RadioButton
              key={i}
              type={type}
              text={value.text}
              selected={answerSelected === i}
              handle={selectComponent}
              parameter={i}
            />
          );                
      })}
    </div>
  );
}

export default RadioButtonGroup


    




