import React,{ useContext } from 'react';
import { AppContext } from "../context/AppContext";

const NECategoryContainer = () => {
    const { appState } = useContext(AppContext);
    const { needExplorerController } = appState;
    const { activeCategory } = needExplorerController;

    const isActive = (cat) => {
        return activeCategory === cat
    }

    return (
        <div className="category-list">
            <ul>
                <li className={`${isActive('general') ? 'active' : ''}`}>General</li>
                <li className={`${isActive('screenUsage') ? 'active' : ''}`}>Screen usage</li>
                <li className={`${isActive('transports') ? 'active' : ''}`}>Transports</li>
                <li className={`${isActive('indoor') ? 'active' : ''}`}>Indoor</li>
                <li className={`${isActive('outdoorActivities') ? 'active' : ''}`}>Outdoor activities</li>
            </ul>
        </div>
    )
}

export default NECategoryContainer;