import React from "react";

const Notes = React.forwardRef(({
  placeholder,
  className,
  handler,
  rows,
  cols,
  value
}, ref ) => {
  return (
    <textarea
      className={`notes ${className ? className : ""}`}
      placeholder={placeholder}
      rows={rows}
      cols={cols}
      onChange={handler ? () => handler(value) : () => {}}
      ref={ref} 
    >
    </textarea>
  );
});

export default Notes;

