import React, { useContext } from "react";
import { AppContext } from "../context/AppContext";
import NEContainer from "../containers/NEContainer"
import NEModalContainer from "../containers/NEModalContainer";
import NEResultContainer from "../containers/NEResultContainer";
import NEFinishedContainer from "../containers/NEFinishedContainer";

const NeedExplorer = () => {
  const { appState } = useContext(AppContext);
  const { needExplorerController } = appState;
  const { pageProgress } = needExplorerController;

  const renderPage = () => {
    switch(pageProgress){
      case 'modal-page' : return <NEModalContainer />
      case 'questionnaire-page' : return <NEContainer />
      case 'finished-page' : return <NEFinishedContainer />
      case 'result-page' : return <NEResultContainer />
      default: return ''
    }
  }

  return (
    <div className="need-explorer">
      { renderPage() }
    </div>
  );
};

export default NeedExplorer;
