const RadioButton = ({ type, text, selected, handle, parameter }) => {
  return (
    <div
      className={`radio-btn ${type} ${selected ? "selected-answer" : ""}`}
      onClick={() => (handle ? handle(parameter) : "")}
    >
      <label className="radio">
        <input
          type="radio"
          name="answers"
          checked={selected}
          onChange={e=>{}}
        />
        <div className="product__text">
          <strong>{text}</strong>
        </div>
        <div className={`product__checkbox`}></div>
      </label>
    </div>
  );
};
 
export default RadioButton;