import React from 'react';

const MeasuresTable = ({
    items
}) => {
    const renderMeasuresTable = () => {
        const tables_jsx = items.map((i,j) => {
            return (
                <div className="measures-tables__table" key={j}>
                    <div className="measures-tables__table--header">
                        {i.headerTitle}
                    </div>
                    <div className="measures-tables__table--content">
                        {i.fields.map((x,y) => {
                            return (
                                <div className="group" key={y}>
                                    <div className="field-name">{x.fieldName}</div>
                                    <div className="field-value">{x.fieldValue}</div>
                                </div>
                            )
                        })}
                    </div>
                </div>
            )
        })

        return tables_jsx
    }

    return (
        <div className="measures-tables">
            {renderMeasuresTable()}
        </div>
    )
}

export default MeasuresTable