import React, { useContext, useEffect, useState } from 'react';
import { AppContext } from '../../context/AppContext';
import Button from '../../components/Button';
import PortalModal from '../../components/PortalModal';
import EducationLanding from './components/EducationLanding';
import EducationControls from './components/EducationControls';
import EducationContent from './components/EducationContent';

const Education = () =>  {
    // VARIABLES
    const { appState, setAppState } = useContext(AppContext);
    const [isOpen, setIsOpen] = useState(false);
    const [controlsView, setControlsView] = useState('expanded');
    const [controlsData, setControlsData] = useState();

    const [view, setView] = useState({
        id: 'landing',
        label: 'Education'
    });
    const B = 'education__toggle-controls';

    // HOOKS
    useEffect( () => {
        setIsOpen(appState.sideBarController.openEducation);
        setView({
            id: 'landing',
            label: 'Education'
        });
    }, [appState.sideBarController.openEducation]);

    // METHODS
    const onClose = () => {
        setAppState({
            ...appState,
            sideBarController: {
                ...appState.sideBarController,
                openEducation: false
            },
            education: {
                environment:'sunlight',
                visualDefect1: null
            }
        });
    }

    const back = () => {
        setView({
            id: 'landing',
            label: 'Education'
        });
        setControlsView('expanded');

        setAppState({
            ...appState,
            education: {
                environment:'sunlight',
                visualDefect1: null
            }
        })
    }

    const toggleControlsView = () => {
        setControlsView(controlsView !== 'collapsed' ? 'collapsed' : 'expanded');
    }

    const renderBackButton = () => {
        if( view.id === 'landing')return null;

        return <Button
            className="basic-icon-btn back-btn"
            text="Back"
            icon="start-arrow"
            handler={back}
        ></Button>
    }

    return (
        <PortalModal 
            headerLeftContent={renderBackButton()}
            open={isOpen} onClose={onClose} title={view.label}>
            
            { view.id === 'landing' && <EducationLanding onSelect={(v) => setView(v)}/> }

            { view.id === 'eye' &&
                <div className="education">
                    
                    <EducationControls 
                        dataChanged={d => setControlsData(d)}
                    view={controlsView}/>

                    <EducationContent controlsData={controlsData} >
                        <button 
                            className={`${B} ${B}--${controlsView}`}
                            onClick={toggleControlsView}>
                                <span></span>
                            </button>
                    </EducationContent>
                </div>
            }


        </PortalModal>
    )
}

export default Education;