import Icon from "../components/Icon";
import {Fragment} from 'react';
const PairContainer = ({ index, items, title }) => {
  return (
    <Fragment>
      <span className={`hl ${index === 1 ? "two" : ""}`}></span>

      <div className={`summary-product__container ${index === 1 ? "two" : ""}`}>
        <div className="summary-product__header">{title}</div>

        <div className="summary-product__description">
          {items.map((item, i) => {
            return (
              <div className="product " key={i}>
                <Icon iconKey={item.icon} />
                <div className="sub">{item.label}</div>
              </div>
            );
          })}
        </div>
      </div>

      <span className={`hl one ${index === 1 ? "two" : ""}`}></span>
    </Fragment>
  );
};
 
export default PairContainer;