import React from 'react';

const TextBox = ({
    value,
    className,
    handler,
    placeholder,
    max,
    disable
}) => {
    return (
        <input 
            type="text"
            className={`text-box ${className}`}
            value={value}
            placeholder={placeholder}
            onChange={handler ? (e)=>handler(e) : ()=>{}}
            maxLength ={max}
            disabled={disable}
        />
    )
}

export default TextBox;