import React from 'react';

const SummaryList = ({
    question,
    answers,
    key
}) => {
    const renderAnswers = () => {
        const spans = answers.map((i,j) => {
            return (
                <span className="answer" key={j}>
                    {i.text}
                </span>
            )
        })

        return spans
    }

    return (
        <div className="summary-list" key={key}>
            <h3 className="question">
                {question}
            </h3>
            {renderAnswers()}
        </div>
    )
}

export default SummaryList