import React from 'react';
import { CarouselProvider, Slider, Slide, ButtonBack, ButtonNext } from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';

const MeasuresCarousel = ({
    imgUrls
}) => {
    return (
        <CarouselProvider
            naturalSlideWidth={100}
            naturalSlideHeight={100}
            totalSlides={3}
        >
            <Slider style={{ borderRadius: "1em" }}>
                {
                    imgUrls.map((i,j) => {
                        return (
                            <Slide index={j} key={j}><img src={i} alt="" style={{width: "inherit", height: "auto"}}/></Slide>
                        )
                    })
                }
            </Slider>
            <div className="button-group">
                <ButtonBack className="backBtn">{`<`}</ButtonBack>
                <ButtonNext className="nextBtn">{`>`}</ButtonNext>
            </div>
        </CarouselProvider>
    )
}

export default MeasuresCarousel