import React, { useContext } from "react";
import Icon from '../components/Icon'
import { Link } from "react-router-dom";
import { routes } from "../constants/routes"; 
import { generateSixDigitsID } from '../misc/utils';
import Button from '../components/Button';
import Modal from '../components/Modal';
import { AppContext } from "../context/AppContext";
import TextBox from "../components/TextBox";
import { useHistory } from "react-router-dom";

const LPBContainer = () => {
  const history = useHistory();
  const { appState, setAppState } = useContext(AppContext);
  const { id, showModalDim, homePageController } = appState;
  const { isStart, modalShow, invalidPopup} = homePageController

  const handleStart = () => {
    setAppState({
      ...appState,
      homePageController: {
        ...homePageController,
        isStart: true
      }
    })
  }

  const handleCloseButton = () => {
    setAppState({
      ...appState,
      homePageController: {
        ...homePageController,
        isStart: false
      }
    })
  }

  const handleModalShow = () => {
    setAppState({
      ...appState,
      showModalDim: !showModalDim,
      homePageController: {
        ...homePageController,
        modalShow: !modalShow
      }
    })
  }
  const handleInvalidShow = () => {
    setAppState({
      ...appState,
      showModalDim: !showModalDim,
      homePageController: {
        ...homePageController,
        invalidPopup: !invalidPopup
      }
    })
  }

  const handleAgeChoice = (choice) => {
    setAppState({
      ...appState,
      id: generateSixDigitsID(),
      homePageController: {
        ...homePageController,
        isStart: false,
        ageChoice: choice
      }
    })
  }

  const startFromPanel = () => {
    return (
      <div className="lpb-container__panel">
        <h1 className="panel-text">Start from</h1>
        <div className="panel-buttons">
          <Button
            className="basic-icon-btn faded-white-bg"
            text="QR Code"
            icon="qr-code"
          ></Button>
          <Button
            className="basic-icon-btn faded-white-bg"
            text="ID"
            icon="id"
            handler={handleModalShow}
          ></Button>
        </div>
      </div>
    )
  }

  const ageCategoryPanel = () => {
    return (
      <div className="lpb-container__category-panel">
          <div className="header">
            <h1 className="question">What's your age category?</h1>
            <Button
              className="icon-btn close"
              icon="close-button"
              handler={handleCloseButton}
            ></Button>
          </div>
          <div className="choices">
            <Link to={routes.LPB.path}>
              <Button
                className="basic-btn faded-white-bg"
                text="Kids"
                handler={() => handleAgeChoice('kids')}
              ></Button>
            </Link>
            <Link to={routes.LPB.path}>
              <Button
                className="basic-btn faded-white-bg"
                text="Young Adult"
                handler={() => handleAgeChoice('young-adult')}
              ></Button>
            </Link>
            <Link to={routes.LPB.path}>
              <Button
                className="basic-btn faded-white-bg"
                text="Adult"
                handler={() => handleAgeChoice('adult')}
              ></Button>
            </Link>
          </div>
      </div>
    )
  }
  
  const handleNext = () => {
    if(parseInt(id) === 0){
      setAppState({
        ...appState,
        homePageController: {
          ...homePageController,
          modalShow: !modalShow,
          invalidPopup: !invalidPopup
        }
      })
    }else{
      setAppState({
        ...appState,
        showModalDim: !showModalDim,
        homePageController: {
          ...homePageController,
          modalShow: !modalShow,
          isStart: false
        }
      });
  
      history.push(routes.LPB.path)
    }    
  }

  /* go to current page left */
  const continueJourney = () =>{
    /* temp */
    setAppState({
      ...appState,
      homePageController: {
        ...homePageController,
        isStart: false
      }
    });

    history.push(routes.LPB.path)
  }

  const handleIDTextBox = (e) => {
    const { value } = e.target
    
    setAppState({
      ...appState,
      id: value
    })
  }

  const removeValue = () =>{
    setAppState({
      ...appState,
      id: ""
    })
  }

  const renderModalInvalid = () =>{
    return(
      <div className="modal-content">
      <div className="btn-header">
            <Button
                className="icon-btn close"
                icon="close-button"
                handler={handleInvalidShow}
            ></Button>
        </div>
        <h2 className="lpb-modal-header invalid">You have entered an invalid  or not existing ID.<br/><br/> Please Try Again</h2>
      </div>
    )
    
  }

  const renderModalContent = () => {
    return (
      <div className="modal-content">
        <div className="btn-header">
            <Button
                className="icon-btn close"
                icon="close-button"
                handler={handleModalShow}
            ></Button>
        </div>
        <h2 className="lpb-modal-header">Start from an ID</h2>
        <div className="text-btn-grp">
          <TextBox
            value={id}
            className="border-blue lpb-id-tbox"
            handler={handleIDTextBox}
            placeholder="Enter ID"
            max={6}
          />
          {
          id.length > 0 && 
            <Button
            className="next-btn compact"
            text="Next"
            icon="arrow-white-blue"
            handler={handleNext}
          ></Button>
         }
        </div>
      </div>
    )
  }

  const renderContinueTextBox = () => {
    return (
      <div className="continue-container">
        <Button
          className="basic-icon-btn exit-btn"
          icon="addIcon"
          handler={removeValue}
        ></Button>
        <TextBox
        value={"ID "+id}
        className="tbox"
        handler={handleIDTextBox}
        max={25}
        disable={true}
        />
        <Button
          className="basic-icon-btn lpb-container__start continue-btn faded-white-bg"
          text="Continue"
          icon="start-arrow"
          handler={continueJourney}
        ></Button>
      </div>
    )
  }

  const renderStartButton = () => {
    return (
      <Button
        className="basic-icon-btn lpb-container__start faded-white-bg"
        text="Start"
        icon="start-arrow"
        handler={handleStart}
      ></Button>
    )
  }

  return (
    <div className="lpb-container">
      <Icon iconKey="glasses"/>
      <h1 className="lpb-container__header">Lens Proposal Builder</h1>
      { 
        !isStart && id.length === 0 && renderStartButton()
      }
      {
        id.length > 0 && renderContinueTextBox()
      }
      {
        !isStart ? startFromPanel() : ageCategoryPanel()
      }
      <Modal className="modal-simple lpb-modal-content" isShow={modalShow} content={renderModalContent()} />
      <Modal className="modal-simple lpb-modal-content" isShow={invalidPopup} content={renderModalInvalid()} />
    </div>
  );
};

export default LPBContainer;
