import React, { useEffect, useState } from 'react';
import './toggle-button.scss';

const ToggleButton = ({
    value,
    options = [],
    onChange
}) =>  {
    // VARIABLES
    const [data, setData] = useState(value);
    
    const B = 'toggle-button';
    const E = (el, mod) => {
        const element = `${B}__${el}`;
        let modifier = '';
        if(mod) {
            modifier = mod.map( x => `${element}--${x}`).join(" ");
        }
        return `${element} ${modifier}`;
    }

    // HOOKS
    useEffect(() => {
        if(onChange && data)onChange(data);
    }, [data])

    // METHODS
    const activeClass = (option) => {
        if(!data) return '';
        return (data === option.value) ? 'selected' : '';
    }

    return (
        <div className={B}>
            {
                options.map((option, index) => (
                    <div className={E('option', [activeClass(option)])} key={index} 
                        onClick={() => setData(option.value)}>
                        {option.label}
                    </div>
                ))
            }
        </div>
    )
}

export default ToggleButton;