import React, { useState, useContext, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import LPBButtons from "../components/LPBButtons";
import { AppContext } from "../context/AppContext";
import Icon from "../components/Icon";
import Button from "../components/Button";
import SelectionButton from "../components/SelectionButton";
import Accordion from "../components/Accordion";
import routes from "../constants/routes";

const LensConsultationContainer = () => {
    const history = useHistory();
    const { appState, setAppState } = useContext(AppContext);
    const { lensConsultation, stepProgress } = appState;
    const { storeCorrect, storeProtect, storeEnhance } = lensConsultation;
    const { activeLabel, progress } = stepProgress;
    const [state, setState] = useState({
      showProducts: false,
      titleBackButtons: "",
      isListOpen: false,
      selectedItem: [],
      holderItems: []
    });
    const {
      showProducts,
      titleBackButton,
      isListOpen,
      selectedItem,
      holderItems
    } = state;

    
    //START: sample temporary product list
    const correct = [{
        productName: "Varilux",
        products: [{
            id: 1,
            title: "Varilux E Series",
            subList: [],
          },
          {
            id: 2,
            title: "Varilux X Series",
            subList: [{
                id: 1,
                title: "Varilux Xclusive 4D",
                key: 2,
              },
              {
                id: 2,
                title: "Varilux X Track",
                key: 2,
              },
              {
                id: 3,
                title: "Varilux X Design",
                key: 2,
              },
            ],
          },
          {
            id: 3,
            title: "Varilux Physio",
            subList: [],
          },
          {
            id: 4,
            title: "Varilux Comfort",
            subList: [],
          },
          {
            id: 5,
            title: "Varilux Liberty",
            subList: [],
          },
        ]
      }

    ];
    const protect = [{
      productName: "Transitions",
      products: [{
        id: 1,
        title: "Transitions",
        subList: [{
            id: 1,
            title: "Transitions Gen 8",
            key: 1,
          },
          {
            id: 2,
            title: "Transitions XTRActive",
            key: 1,
          },
        ],
      }],
    }];
    const enhance = [{
      productName: "Crizal",
      products: [{
        id: 1,
        title: "Crizal",
        subList: [{
          id: 1,
          title: "Crizal Easy UV",
          key: 1,
        }, ],
      }],
    }];
    //END: sample temporary product list

    //temporary
    useEffect(()=>{
      switch(activeLabel){
        case "correct":{
          return(
            setState({
              ...state,              
              holderItems: correct
            })
          );
        }
        case "protect": {
          return(
            setState({
              ...state,              
              holderItems: protect
            })
          );

        }
        case "enhance": {
          return(
            setState({
              ...state,              
              holderItems: enhance
            })
          );
        }
        default: 
          return;
      }
    },[activeLabel])
    
    const getCurrentPanel = () =>{
        switch (activeLabel) {
          case "correct":
          case "protect":
          case "enhance":
            return productPanel();
          default:
            return mainPanel();
        }
    }

    const navigationPanel = (value) =>{
        if (value === "products" && !showProducts)
          setAppState({
            ...appState,
            stepProgress: {
              ...stepProgress,
              activeLabel: "Prescription",
              progress: storeCorrect ? progress : progress - 30,
            },
          });
        else {
          setState({
            ...state,
            showProducts: !showProducts,
            titleBackButton: value === "products" ? activeLabel : value,
          });
        }
    }
    
    const handleToggle = (id) =>{
      const open = selectedItem.some(item => item !== id) && selectedItem.length > 0 ? true : !isListOpen;
      const temp = selectedItem.some(item => item === id)  ? selectedItem.filter(item=> item !== id) : [...selectedItem, id];
      setState({
        ...state,
        isListOpen: open,
        selectedItem: temp
      });
    }

    const modifyProducts = (value,name) => {
      const valueProgress = !name ? (progress - 30) : (progress);
        switch (value) {
          case "correct":
            return setAppState({
              ...appState,
              lensConsultation: {
                ...lensConsultation,
                storeCorrect: name,
              },
              stepProgress: {
                ...stepProgress,
                progress: valueProgress,
              },
            });
          case "protect":
            return setAppState({
              ...appState,
              lensConsultation: {
                ...lensConsultation,
                storeProtect: name,
              },
              stepProgress: {
                ...stepProgress,
                progress: valueProgress,
              },
            });
          case "enhance":
            return setAppState({
              ...appState,
              lensConsultation: {
                ...lensConsultation,
                storeEnhance: name,
              },
              stepProgress: {
                ...stepProgress,
                progress: valueProgress - 10,
              },
            });

          default:
        }
      }
    

    const productPanel = () => {
        return (
          <div>
            <Button
              className="back-btn"
              text={titleBackButton ? titleBackButton : activeLabel}
              icon="start-arrow"
              handler={navigationPanel}
              value="products"
            ></Button>
            <div className="product__list">
              {/* second panel choose which product brand */}
              {!showProducts && (
                <div>
                  {holderItems.map((items,i)=>{
                    return(
                      <Button
                        key={i}
                        className="basic-icon-btn product-btn"
                        text={items.productName}
                        icon="demo-rightIcon"
                        handler={navigationPanel}
                        value={items.productName.toLowerCase()}
                      ></Button>
                    );
                  })}
                  
                  <Button
                    className="basic-icon-btn product-btn"
                    text="Eyezen"
                    icon="demo-rightIcon"
                    handler={navigationPanel}
                    value={"Eyezen"}
                  ></Button>
                </div>
              )}
              {/* third panel for more products under the chosen brand */}
              {showProducts && (
                <div>
                  <div className="vl"></div>
                  {/* Correct Products for varilux *sample */}
                  {holderItems.map((value) => {
                    return value.products.map( item =>
                      <div key={item.id}>
                        <Accordion
                          toggleOpen={isListOpen}
                          selectedItem={selectedItem}
                          handler={handleToggle}
                          id={item.id}
                          title={item.title}
                        />
                        {/* sub headers of the product selected*/}
                        {isListOpen && selectedItem.some(value => value === item.id) 
                          ? item.subList.map((item) => {
                              return (
                                <SelectionButton
                                  key={item.id}
                                  title={item.title}
                                  type="radio"
                                  handler={modifyProducts}
                                  parameter={activeLabel}
                                  borderDisplay={true}
                                />
                              );
                            })
                          : ""}
                        <div className="vl"></div>
                      </div>
                    );
                  })}
                </div>
              )}
            </div>
          </div>
        );
    };
    {/* panel to be displayed after choosing products */}
    const answeredPanel = (title, value) =>{
      return (
        <div className="package__answered">
          <div className="package-title">{title}</div>
          <div className="package__answered--container">
            <div className="correct-product">{value}</div>
            <div className="content-icon" onClick={() => modifyProducts(title, "")}>
              <Icon iconKey="trash-icon"></Icon>
            </div>
            {title === "Protect" && (
              <div className="content-icon">
                <Icon iconKey="addIcon"></Icon>
              </div>
            )}
          </div>
        </div>
      );
    }
    const gotoPackageOffer = () => {
      const lensConsultationData = appState.lensConsultation;
      const keys = Object.keys(lensConsultationData);
      let withDataFlag = false;
      keys.forEach(key => {
        if(lensConsultationData[key].length > 0 ) {
          withDataFlag = true;
        }
      });
      if(!withDataFlag) {
        return;
      }
      // alert('go');
      history.push(routes.PACKAGE_OFFER.path);
    }
    const mainPanel = () =>{
        return (
          <div>
            <Link onClick={gotoPackageOffer}>
              <div className="packagebtn">
                <span>Packaged Offer</span>
                <Icon iconKey="packageIcon" />
              </div>
            </Link>
            <div className="packagelist">
              {!storeCorrect ? (
                <LPBButtons
                  title={"Correct"}
                  selector={"white"}
                  displayExtra={"hide"}
                  icon={"addIcon"}
                  displayFooter={"hide"}
                />
              ) : (
                answeredPanel("correct", storeCorrect)
              )}
              <div className="vl"></div>
              {!storeProtect ? (
                <LPBButtons
                  title={"Protect"}
                  selector={"white"}
                  displayExtra={"hide"}
                  icon={"addIcon"}
                  displayFooter={"hide"}
                />
              ) : (
                answeredPanel("protect", storeProtect)
              )}
              <div className="vl"></div>
              {!storeEnhance ? (
                <LPBButtons
                  title={"Enhance"}
                  selector={"white"}
                  displayExtra={"hide"}
                  icon={"addIcon"}
                  displayFooter={"hide"}
                />
              ) : (
                answeredPanel("enhance", storeEnhance)
              )}
            </div>
          </div>
        );
    }
    return <div>{getCurrentPanel()}</div>;
}
 
export default LensConsultationContainer;