import Icon from "./Icon";
import React, {useContext, useState, useEffect } from "react";
import { AppContext } from "../context/AppContext";
import { useLocation, useHistory, Link } from 'react-router-dom';
import { routes } from '../constants/routes';

const NavBar = () => {
  const { appState, setAppState } = useContext(AppContext);
  const { sideBarController, showModalDim } = appState;    
  const { openSideBar } = sideBarController;  
  const [state, setState] = useState({changeNav: false});
  const {changeNav} = state;
  const location = useLocation();
  const history = useHistory();

  useEffect(() => {
    if (
      location.pathname === routes.LEGAL_NOTICES.path ||
      location.pathname === routes.MY_COMPANION.path
    )
      setState({ changeNav: false });
    else setState({ changeNav: true });
  }, [location.pathname]);

  const HeaderText = () =>  {
    // setState({...state, changeNav: true});
      const routesKeys = Object.keys(routes);
      const activeRouteKey = routesKeys.filter( x => routes[x].path === location.pathname)[0];
      if(!activeRouteKey)return "";
      return routes[activeRouteKey].navbarTitle;     
  }

  const closeScreen = (event) =>{
    event.preventDefault();
    setState({ ...state, changeNav: true });
    history.push(routes.HOMEPAGE.path);
  }

  const openNavigation = (event) =>{
      event.preventDefault();
      if (location.pathname !== routes.HOMEPAGE.path){
        setAppState({
          ...appState,
          showModalDim: true,
          sideBarController: {
            ...sideBarController,
            modalShow: true
          },
        });
      }
      else{
        setAppState({
          ...appState,
          showModalDim: !showModalDim,
          sideBarController: {
            ...sideBarController,
            openSideBar: !openSideBar,
          },
        });
      }
        
  }
  
  return (

    <div className="navbar">
      <Link className="navbar__logo" to="/">
        <Icon iconKey="logo" />
      </Link>
      <div className="navbar__center">
        <h2>{HeaderText()}</h2>
      </div>

      <div className="navbar__right">
        {changeNav ? (
          <div>
            <a href="/">
              <Icon iconKey="dropbox" />
            </a>
            <a href="/">
              <Icon iconKey="flip" />
            </a>
            <span className="vl"></span>
            <a href="/" onClick={(e) => openNavigation(e)}>
              <Icon iconKey="hamburger" />
            </a>
          </div>
        ) : (
          <a href="/" className="exit-btn" onClick={(e) => closeScreen(e)}>
            <Icon iconKey="close-blue-gray-icon" />
          </a>
        )}
      </div>
    </div>
  );
};
 
export default NavBar;