import Icon from "../components/Icon";
import { routes } from "../constants/routes"; 
import { useLocation } from "react-router-dom";
import { AppContext } from "../context/AppContext";
import React, { useContext } from "react";

const Accordion = ({ selectedItem, handler, title, id, toggleOpen }) => {
  const { appState } = useContext(AppContext);
  const { myCompanionController } = appState;
  const { selectedCategory, showPrice } = myCompanionController;
  const location = useLocation();
  return (
    <div
      className={
        toggleOpen && selectedItem.some(item => item === id)
          ? "accordion__list--card selected"
          : "accordion__list--card"
      }
      onClick={() => (handler ? handler(id) : "")}
    >
      {toggleOpen && selectedItem.some(item => item === id) ? <div className="border"></div> : ""}

      <div className="accordion__list--content">
        {title}
        {location.pathname === routes.MY_COMPANION.path &&
          showPrice &&
          (selectedCategory === "correct" ||
            selectedCategory === "protect" ||
            selectedCategory === "enhance") && (
            <span className="price-view">100 </span>
          )}

        {toggleOpen && selectedItem.some(item => item === id) ? (
          <div className="arrow-up"><Icon iconKey="arrow-up" /></div>
        ) : (
          <div className="arrow-down"><Icon iconKey="arrow-down" /></div>
        )}
      </div>
    </div>
  );
};
 
export default Accordion;