import React, { useContext, useEffect, useRef, Fragment } from "react";
import { useHistory } from "react-router-dom";
import Icon from "../components/Icon";
import { AppContext } from "../context/AppContext";
import {sidebar}  from '../misc/sidebar-nav';
const SideBar = () => {
    const history = useHistory();
    const { appState, setAppState } = useContext(AppContext);
    const { sideBarController } = appState;
    const { openSideBar, selectedMain, subNav, right } = sideBarController;
    
    const ref = useRef(null);
    
    useEffect(() => {
      document.addEventListener("mousedown", closeFirstSide, true);
      return () => {
        document.removeEventListener("mousedown", closeFirstSide, true);
      };
    }, []);

    const closeFirstSide = (event) =>{
      if (ref.current && ref.current.contains(event.target)) {
        setAppState({
          ...appState,
          showModalDim: true,
          sideBarController: {
            ...sideBarController,
            subNav: [],
            right: "0",
            openSideBar: true,
            selectedMain: null
          },
        });
      }
    }

    const linkTo = (url, subNavigation, key) =>{
        if (subNav.length > 0 && subNavigation !== undefined) {
          setAppState({
            ...appState,
            showModalDim: true,
            sideBarController: {
              ...sideBarController,
              subNav: [],
              right: "0",
              openSideBar: true,
              selectedMain: null,
            },
          });
        } else if (subNavigation === undefined || subNavigation.length === 0) {
          if(url)history.push(url);
          setAppState({
            ...appState,
            showModalDim: false,
            sideBarController: {
              ...sideBarController,
              openSideBar: false,
              openEducation: key === 1 ? true: false,
              selectedMain: null,
              right: "0",
              subNav: [],
            },
          });
        } else {
          setAppState({
            ...appState,
            sideBarController: {
              ...sideBarController,
              selectedMain: key,
              subNav: [...subNav, ...subNavigation],
              right:
                subNavigation === undefined || subNavigation.length === 0
                  ? "0"
                  : "19.6%",
            },
          });
        }
    }

    return (
      <Fragment>
        <div
          className={`side-bar ${openSideBar ? "display" : ""}`}
          style={{ right: right }}
        >
          <p className="side-bar__title">My Practice</p>
          <div className="hl"></div>
          <div
            ref={ref}
            className={`dimSidebar ${
              subNav.length > 0 && openSideBar ? "active" : ""
            }`}
          ></div>
          {sidebar.map((items, i) => {
            return (
              <div
                key={i}
                className={`side-bar__link ${
                  selectedMain === i ? "active" : ""
                }`}
                onClick={() => linkTo(items.url, items.subNavigation, i)}
              >
                <div className="icon">
                  <Icon iconKey={items.icon} />
                </div>
                <p>{items.label}</p>
              </div>
            );
          })}
        </div>

        {subNav.length > 0 && openSideBar && (
          <div className="side-bar display">
            <p className="side-bar__title"></p>
            <div></div>
            {subNav.map((items, i) => {
              return (
                <div
                  key={i}
                  className="side-bar__link"
                  onClick={() => linkTo(items.url, items.subNavigation, i)}
                >
                  <div className="icon">
                    <Icon iconKey={items.icon} />
                  </div>
                  <p>{items.label}</p>
                </div>
              );
            })}
          </div>
        )}
      </Fragment>
    );
}
 
export default SideBar;