import React from 'react';

const TextBoxOneBorder = ({
    value,
    className,
    handler,
    placeholder,
    max,
    disable,
    label,
    name
}) => {
    return (
        <div className="text-box-one-border-container">
            {value && 
                <label className="text-box-one-border-container__label">
                    {label}
                </label>
            }
            <input 
                type="text"
                name={name}
                className={`text-box-one-border-container__tb ${className}`}
                value={value}
                placeholder={placeholder}
                onChange={handler ? (e)=>handler(e) : ()=>{}}
                maxLength ={max}
                disabled={disable}
            />
        </div>
    )
}

export default TextBoxOneBorder;