import React, { createContext, useState, useContext, useEffect } from "react";

export const AppContext = createContext()

//Hooks
export const useAppContext = () => {
    const { appState, setAppState } = useContext(AppContext)
    return { appState, setAppState }
}

const AppContextProvider = (props) => {
    const [appState, setAppState] = useState({
      showModalDim: false,
      title: "",
      id: "",
      sideBarController: {
        openSideBar: false,
        openEducation: false,
        selectedMain: Number,
        subNav: [],
        right: "0",
        modalShow: false,
      },
      myCompanionController: {
        selectedCategory: "",
        selectedItems: [],
        isListOpen: false,
        selectedId: [],
        showPrice: false,
        modalShow: false,
      },
      homePageController: {
        isStart: false,
        modalShow: false,
        invalidPopup: false,
        ageChoice: "",
      },
      lpbController: {
        modalShow: false,
        invalidPopup: false,
      },
      needExplorerController: {
        activeCategory: "general" || "",
        pageProgress: "modal-page",
        progress: 10,
        isSummaryModalShow: false,
      },
      lensConsultation: {
        storeCorrect: "",
        storeProtect: "",
        storeEnhance: "",
      },
      prescriptionController: {
        active: "standard",
        isNext: false,
        isImportModalShow: false,
      },
      measuresController: {
        pageProgress: 1,
        modalActive: false,
        showSummary: false,
        isImportModalShow: false,
      },
      deliveryController: {
        pageProgress: 'modal-page',
        exportModalShow: false,
        scrollCoordinate: 0,
        sectionCoordinates: {
          prescription: {
            section: "Prescriptions",
            active: true
          },
          pair1: {
            section: "Pair1",
            active: false
          },
          pair2: {
            section: "Pair2",
            active: false
          },
          visionTest: {
            section: "VisionTests",
            active: false
          },
          advices: {
            section: "Advices",
            active: false
          },
        }
      },
      selectedPackage: "Package01",
      stepProgress: {
        activeLabel: "Prescription",
        progress: 0,
      },
      education: {
        environment: 'sunlight',
        visualDefect1: null,
        item: 'off',
        lens:'off'
      }
    });
    useEffect(() => {
        if (localStorage.getItem("productsSelected")){
            const getList = JSON.parse(localStorage.getItem("productsSelected"));
            setAppState({
                ...appState,
                lensConsultation:{
                  storeCorrect: getList[0].storeCorrect,
                  storeProtect: getList[0].storeProtect,
                  storeEnhance: getList[0].storeEnhance,
                }
                
            });
        }
    }, []);

    useEffect(() => {
      const productList = [
        {
          storeCorrect: appState.lensConsultation.storeCorrect,
          storeProtect: appState.lensConsultation.storeProtect,
          storeEnhance: appState.lensConsultation.storeEnhance,
        }
      ];
      localStorage.setItem("productsSelected", JSON.stringify(productList));
    }, [appState.lensConsultation.storeCorrect, appState.lensConsultation.storeProtect, appState.lensConsultation.storeEnhance]);
    
    return (
        <AppContext.Provider value={{
            appState,
            setAppState
        }}>
            {props.children}
        </AppContext.Provider>
    )
}


export default AppContextProvider