import React, { useContext, useEffect, useRef } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom"

import NavBar from '../components/NavBar';
import BreadCrumbs from "./Breadcrumbs";
import SideBar from "./SideBar";

import Modal from '../components/Modal';
import Button from "../components/Button";

import HomePage from "../pages/Homepage";
import LensProposalBuilder from "../pages/LensProposalBuilder";
import LensConsultation from "../pages/LensConsultation";
import PackageOffer from "../pages/PackageOffer/PackageOffer";
import NeedExplorer from "../pages/NeedExplorer";
import LensThickness from "../pages/LensThickness";
import Prescription from "../pages/Prescription";
import LensSummary from "../pages/LensSummary";
import Measures from "../pages/Measures";
import LegalNotices from "../pages/LegalNotices";
import MyCompanion from "../pages/MyCompanion";
import PracticeID from "../pages/PracticeID";

import { AppContext } from "../context/AppContext";

import "../styles/index.scss";

import { routes } from '../constants/routes';
import Education from "../pages/education/Education";
import Delivery from "../pages/Delivery/Delivery";

const App = () => {
  const { appState, setAppState } = useContext(AppContext);
  const { showModalDim, sideBarController } = appState;
  const { modalShow, openSideBar } = sideBarController;

  const ref = useRef(null);

  useEffect(() => {
    document.addEventListener("mousedown", closeSideBar, true);
    return () => {
      document.removeEventListener("mousedown", closeSideBar, true);
    };
  }, []);

  const closeSideBar = (event) => {
    if (ref.current && ref.current.contains(event.target)) {     
      setAppState({
        ...appState,
        showModalDim: false,
        sideBarController: {
          ...sideBarController,
          openSideBar: false,
          subNav: [],
          right: '0'
        },
      });
    }
    
  };

  const closeModal = () =>{
    setAppState({
      ...appState,
      showModalDim: false,
      sideBarController: {
        ...sideBarController,
        modalShow: false,
      },
    });
  }

  const openNavigation = () =>{
    setAppState({
      ...appState,
      sideBarController: {
        ...sideBarController,
        openSideBar: !openSideBar,
        modalShow: !modalShow,
      },
    });
  }

  const renderSaveModal = () =>{
    return (
      <div className="save-modal-content">
        <div className="btn-header">
          <Button
            className="icon-btn close"
            icon="close-button"
            handler={closeModal}
          ></Button>
        </div>
        <h2 className="lpb-modal-header">
          Do you want to save your current journey?
        </h2>
        <div className="save-text-grp">
          <Button
            className="next-btn compact"
            text="Exit"
            handler={openNavigation}
          ></Button>
          <Button
            className="next-btn compact"
            text="Save"
            handler={openNavigation}
          ></Button>
        </div>
      </div>
    );
  }

  return (
    <Router>
      <NavBar />
      <BreadCrumbs />
      <SideBar/>
      <Route exact path={routes.HOMEPAGE.path} component={HomePage} />
      <Route exact path={routes.LPB.path} component={LensProposalBuilder} />
      <Route
        exact
        path={routes.LENS_CONSULTATION.path}
        component={LensConsultation}
      />
      <Route exact path={routes.PACKAGE_OFFER.path} component={PackageOffer} />
      <Route exact path={routes.NEED_EXPLORER.path} component={NeedExplorer} />
      <Route exact path={routes.LENS_THICKNESS.path} component={LensThickness} />
      <Route exact path={routes.PRESCRIPTION.path} component={Prescription} />
      <Route exact path={routes.LENS_SUMMARY.path} component={LensSummary} />
      <Route exact path={routes.MEASURES.path} component={Measures} />
      <Route exact path={routes.LEGAL_NOTICES.path} component={LegalNotices} />
      <Route exact path={routes.MY_COMPANION.path} component={MyCompanion} />
      <Route exact path={routes.PRACTICE_ID.path} component={PracticeID} />
      <Route exact path={routes.DELIVERY.path} component={Delivery} />
      <div ref={ref} className={`dim ${showModalDim ? "showDim" : ""}`}></div>
      <Modal
        className="modal-simple lpb-modal-content"
        isShow={modalShow}
        content={renderSaveModal()}
      />

      <Education/>

    </Router>
  );
};

export default App;
