import React, { Children } from 'react';
import ReactDOM from 'react-dom';
import Button from './Button';

const PortalModal = ({
    open,
    onClose,
    headerLeftContent,
    title,
    size = 'large', //FULL, LARGE, MEDIUM, SMALL, default large
    children,
}) =>  {
    if(!open) return null;

    // VARIABLES

    // HOOKS

    // METHODS
    const closeModal = () => {
        if(onClose) onClose();
    }

    return ReactDOM.createPortal(
        <>
            <div className="portal-modal-overlay"></div>
            <div className={'portal-modal portal-modal--' + size}>
                <div className="portal-modal__header">
                    
                    {headerLeftContent}
                    

                    <h1 className="portal-modal__header-title">{title}</h1>

                    <Button
                        className="icon-btn close"
                        icon="close-button"
                        handler={closeModal}
                    ></Button>

                </div>
                <div className="portal-modal__content">
                    {children}
                </div>
            </div>
        </>

        ,document.getElementById('portal')
    )
}

export default PortalModal;