import React, { useState, useEffect } from 'react';
import Slider from 'react-input-slider';

const SliderGroup = ({
    items
}) => {
    const [state, setState] = useState();

    useEffect(() => {
        const item = items.map(i => {
            return {start: i.start}
        })

        setState(item)
    }, [items])

    const sliderStyle = {
        track: {
            backgroundColor: "#CFD3D7"
        },
        active: {
            backgroundColor: "#0386D6",
            borderRadius: "1em"
        },
        thumb: {
            width: 20,
            height: 20,
            opacity: 1
        }
    }

    const handleOnChange = (j, data) => {

        state[j] =  { 
            start: parseFloat(data.x.toFixed(2))
        }

        setState({
            ...state
        })
    }

    const renderItems = () => {
        const item = items.map((i,j)=>{
            return (
                <div className="slider-item" key={j}>
                    <p className="question-text">{i.text}</p>
                    <Slider
                        className="slider"
                        axis="x"
                        xstep={0.1}
                        xmin={0}
                        xmax={1}
                        x={state[j].start}
                        onChange={(data) => handleOnChange(j, data)}
                        styles={sliderStyle}
                        key={j}
                    />
                </div>
            )
        })

        return item;
    }

    return (
        <div className="slider-items">
            {state ? renderItems() : ''}
        </div>
    )
}

export default SliderGroup;