import { AppContext } from "../context/AppContext";
import React, { Fragment, useContext } from "react";
import { companionList, companionButtons } from "../misc/mycompanion";

import Icon from "../components/Icon";
import Button from '../components/Button';
import Accordion from "../components/Accordion";
import SelectionButton from "../components/SelectionButton";
import Modal from "../components/Modal";

const MyCompanion = () => {
  const { appState, setAppState } = useContext(AppContext);
  const { myCompanionController } = appState;
  const {
    selectedCategory,
    isListOpen,
    selectedId,
    showPrice,
    modalShow
  } = myCompanionController;
  const handleToggle = (id) =>{
    const open = selectedId.some(item => item !== id) && selectedId.length > 0 ? true : !isListOpen;
    const temp = selectedId.some(item => item === id)  ? selectedId.filter(item=> item !== id) : [...selectedId, id];
    setAppState({
      ...appState,
      myCompanionController: {
        ...myCompanionController,
        selectedId : temp,
        isListOpen: open
      },
    });
  }
  const openList = (category) =>{
    setAppState({
      ...appState,
      myCompanionController: {
        ...myCompanionController,
        selectedCategory: category,
      },
    });
  }

  const displayPrice = () =>{
    setAppState({
      ...appState,
      myCompanionController: {
        ...myCompanionController,
        showPrice: !showPrice,
      },
    });
  }

  const addItems = () => {};
  const saveProducts = () => {
    setAppState({
      ...appState,
      showModalDim: true,
      myCompanionController: {
        ...myCompanionController,
        modalShow: !modalShow,
      },
    });
  };
  
  const renderButtons = (value, key) =>{
    if(selectedCategory === "lensThickness"){
      return(<SelectionButton
        key={key}
        title={value.label}
        // handler={addItems}
        parameter={value}
        borderDisplay={true}
        type="checkbox"
      />);
    }
    else{
      return(<Accordion
        toggleOpen={isListOpen}
        selectedItem={selectedId}
        handler={handleToggle}
        id={key}
        key={key}
        title={value.label}
      />);
    }
  }
  
  const renderModalContent = () => {
    return(
      <div className="save-modal-content one-line">
        <Icon iconKey="check-icon" />
        <h2>Saved</h2>
      </div>
    );
  };
    return (
      <div className="my-companion">
        <div className="my-companion__center">
          <div className="my-companion__left">
            {companionButtons.map((btn, i) => {
              return btn.btnGroup.length > 0 ? (
                <Fragment key={i}>
                  <div className="hl"></div>
                  <div className="btn-group" >
                    {btn.btnGroup.map((sub, key) => {
                      return (
                        <Button
                          key={key}
                          className={`companion-btn ${
                            selectedCategory === sub.value ? "active" : ""
                          }`}
                          text={sub.label}
                          icon={sub.icon}
                          value={sub.value}
                          handler={openList}
                        />
                      );
                    })}
                  </div>
                </Fragment>
              ) : (
                <Button
                  key={i}
                  className={`companion-btn ${
                    selectedCategory === btn.value ? "active" : ""
                  }`}
                  text={btn.label}
                  icon={btn.icon}
                  handler={openList}
                  value={btn.value}
                ></Button>
              );
            })}
          </div>
          <div className="my-companion__right">
            {companionList
              .filter((item) => selectedCategory === item.category)
              .map((item, i) => {
                return item.products.map((pr, key) => (
                  <div key={key}>
                    <div className="vl"></div>
                    {renderButtons(pr, key)}
                    {isListOpen && selectedCategory !== "lensThickness" &&
                       selectedId.some(item => item === key) &&
                      pr.list.map((ls, id) => {
                        return (
                          <SelectionButton
                            key={id}
                            title={ls.label}
                            handler={addItems}
                            parameter={ls.label}
                            borderDisplay={true}
                            type="checkbox"
                          />
                        );
                      })}
                  </div>
                ));
              })}
          </div>
        </div>
        <div className="hl footer"></div>
        <div className="my-companion__footer">
          <div className="first">
            <Button
              className={`back-btn price-btn ${showPrice ? "active" : ""}`}
              text={showPrice ? "Price ON" : "Price OFF"}
              handler={displayPrice}
            />
            {showPrice && (
              <Button
                className="next-btn recommended"
                text="Essilor recommended prices"
                icon={"companion-recommended"}
              />
            )}
          </div>

          <Button
            className="next-btn"
            handler={saveProducts}
            text="Save"
            icon={"check-icon"}
          />
        </div>
        <Modal
          className="modal-simple lpb-modal-content"
          isShow={modalShow}
          content={renderModalContent()}
        />
      </div>
    );
}
 
export default MyCompanion;