

import React, { useState, useContext, useEffect } from "react";
import LCContainer from "../containers/LCContainer";
import LensLeftContainer from "../containers/LensLeftContainer";

import { AppContext } from "../context/AppContext";

const LensConsultation = () => {
    const { appState } = useContext(AppContext);
    const { lensConsultation, stepProgress } = appState;
    const { storeCorrect, storeProtect, storeEnhance } = lensConsultation;
    const { activeLabel } = stepProgress;
    const [state, setState] = useState({
      displayOption: false
    });
    const {displayOption} = state;
    
    useEffect(() => {
      if ((storeCorrect !== "" || storeEnhance !== "" || storeProtect !== "") && activeLabel === "Prescription") {
        setState({displayOption: true});
      }
      else{
        setState({ displayOption: false });
      }
    }, [storeCorrect, storeProtect, storeEnhance, activeLabel]);

    return (
      <div className="lens">
        <LensLeftContainer
          displayOption={displayOption}
          displaySlider={storeProtect}
          demobBtn={true}
          route={"lens-thickness"}
        />
        {/* dynamic container right part of lens consultation */}
        <div className="lens__right">
          <LCContainer />
        </div>
      </div>
    );
}
 
export default LensConsultation;