import React, { useContext, useRef, useEffect } from 'react';
import { AppContext } from "../../../context/AppContext";
import Button from "../../../components/Button";
import PrescriptionContent from './PrescriptionContent';
import PairContent from './PairContent';
import VisionTest from './VisionTest';
  
const DeliveryModalContainer = () => {
    const { appState, setAppState } = useContext(AppContext);
    const { deliveryController, showModalDim } = appState
    const { pageProgress, exportModalShow, scrollCoordinate, sectionCoordinates } = deliveryController
    const { prescription, pair1, pair2, visionTest, advices } = sectionCoordinates

    const prescriptionsRef = useRef(null);
    const pair1Ref = useRef(null);
    const pair2Ref = useRef(null);
    const visionTestRef = useRef(null);
    const advicesRef = useRef(null);
    const parentRef = useRef(null);

    useEffect(()=>{
        const { offsetTop: prescriptionTop } = prescriptionsRef.current
        const { offsetTop: pair1Top } = pair1Ref.current
        const { offsetTop: pair2Top } = pair2Ref.current
        const { offsetTop: visionTestTop } = visionTestRef.current
        const { offsetTop: advicesTop } = advicesRef.current

        var coordinates = scrollCoordinate+50

        setAppState({
            ...appState,
            deliveryController: {
                ...deliveryController,
                sectionCoordinates: {
                    prescription: {
                        ...prescription,
                        active: prescriptionTop <= coordinates && pair1Top >= coordinates,
                    },
                    pair1: {
                        ...pair1,
                        active: pair1Top <= coordinates && pair2Top >= coordinates ,
                    },
                    pair2: {
                        ...pair2,
                        active: pair2Top <= coordinates && visionTestTop >= coordinates
                    },
                    visionTest: {
                        ...visionTest,
                        active: visionTestTop <= coordinates && advicesTop >= coordinates,
                    },
                    advices: {
                        ...advices,
                        active: advicesTop <= coordinates 
                    }
                }
            }
        })
    },[scrollCoordinate])

    const handleScroll = () => {
        setAppState({
            ...appState,
            deliveryController: {
                ...deliveryController,
                scrollCoordinate: parentRef.current.scrollTop
            }
        })
    };
      
    const scrollTo = ele => {
        ele.scrollIntoView({
            behavior: "smooth",
            block: "start",
        });

        if(/Android|webOS|iPhone|iPad|Mac|Macintosh|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ) {
            ele.scrollTop = 0
        }
    };

    const handleBack = () => {
        setAppState({
            ...appState,
            deliveryController: {
                ...deliveryController,
                pageProgress: 'modal-page'
            }
        })
    }

    const handleExportModal = () => {
        setAppState({
            ...appState,
            showModalDim: !showModalDim,
            deliveryController: {
                ...deliveryController,
                exportModalShow: !exportModalShow
            }
        })
    }

    const checkIsSummaryOpen = () => {
        return pageProgress === 'summary-page' ? 'summaryOpen' : ''
    }

    return (
        <div className={`delivery__summary-page ${checkIsSummaryOpen()}`} ref={parentRef} onScroll={handleScroll}>
            <div className="delivery__summary-page--navbar">
                <ul>
                    <li>
                        <Button
                        icon="start-arrow"
                        className="icon-btn back-modalPage"
                        handler={handleBack}
                        ></Button>
                    </li>
                   <li 
                    onClick={() => scrollTo(prescriptionsRef.current)}
                    className={`item ${prescription.active ? 'active' : ''}`}>
                    Prescriptions
                   </li>
                   <li 
                    onClick={() => scrollTo(pair1Ref.current)}
                    className={`item ${pair1.active ? 'active' : ''}`}>
                    Pair 1
                   </li>
                   <li 
                    onClick={() => scrollTo(pair2Ref.current)}
                    className={`item ${pair2.active ? 'active' : ''}`}>
                    Pair 2
                   </li>
                   <li 
                    onClick={() => scrollTo(visionTestRef.current)}
                    className={`item ${visionTest.active ? 'active' : ''}`}>
                    Vision Tests
                   </li>
                   <li 
                    onClick={() => scrollTo(advicesRef.current)}
                    className={`item ${advices.active ? 'active' : ''}`}>
                    Advices
                   </li>
                </ul>
                <div className="btn-grp">
                    <div className="price-label">700 </div>
                    <Button
                        className="next-btn compact"
                        text="Save & Export"
                        icon="arrow-white-blue"
                        handler={handleExportModal}
                    ></Button>
                </div>
            </div> 
            <div className="delivery__summary-page--body">
                <div className="section" id="Prescriptions" ref={prescriptionsRef} >
                    <PrescriptionContent/>
                </div>
                <div className="section" id="Pair1" ref={pair1Ref} >
                    <PairContent pair={1}/>
                </div>
                <div className="section" id="Pair2" ref={pair2Ref} >
                    <PairContent pair={2}/>
                </div>
                <div className="section" id="VisionTests" ref={visionTestRef} >
                    <VisionTest/>
                </div>
                <div className="section" id="Advices" ref={advicesRef} >
                    Advices
                </div>
            </div> 

        </div>
    )
}
  

export default DeliveryModalContainer;