import React, { useContext, useEffect, useState } from 'react';
import ToggleRadio from '../../../components/ui/toggleRadio/ToggleRadio';
import { AppContext } from '../../../context/AppContext';
import EducationStep from './EducationStep';

const EducationContent = ({
    children
}) =>  {

    const { appState, setAppState } = useContext(AppContext);
    const [item, setItem] = useState('off');
    const [lens, setLens] = useState('off');

    // VARIABLES
    const updateItemState = (value) => {
        setItem(value);
        setAppState({
            ...appState,
            education: {
                ...appState.education,
                item: value
            }
        })
    }

    const updateLensState = (value) => {
        setLens(value);
        setAppState({
            ...appState,
            education: {
                ...appState.education,
                lens: value
            }
        })
    }

    const educationSteps = [
        {
            instruction: 'First Etiam gravida est vel dolor finibus, at gravida nibh consectetur. Nunc feugiat ut metus at vestibulum. Aliquam in condimentum eros. Aliquam mollis nibh arcu. Etiam gravida est vel dolor.',
            guide: <>
                <ToggleRadio value={item} onChange={v => updateItemState(v)}/>
                <label className={item === 'on' ? 'active' : ''}>Activate the item to see the impact</label>
            </>,
        },
        {
            instruction: 'Second Etiam gravida est vel dolor finibus, at gravida nibh consectetur. Nunc feugiat ut metus at vestibulum. Aliquam in condimentum eros. Aliquam mollis nibh arcu.',
            guide: <>
                <div className="circle">
                    <span className="aarrow aarrow--right aarrow--white"/>
                </div>
                <label className="active">Move the item to understand what happens</label>
            </>
        },
        {
            instruction: 'Third Etiam gravida est vel dolor finibus, at gravida nibh consectetur. Nunc feugiat ut metus at vestibulum. Aliquam in condimentum eros. Aliquam mollis nibh arcu. Aliquam',
            guide: <>
                <ToggleRadio value={lens} onChange={v => updateLensState(v)}/>
                <label className={lens === 'on' ? 'active' : ''}>Add Essilors lenses to see the difference</label>
            </>
        },
        {
            instruction: 'Fourth Etiam gravida est vel dolor finibus, at gravida nibh consectetur. Nunc feugiat ut metus at vestibulum. Aliquam in condimentum eros. Aliquam mollis nibh arcu.',
            guide: <>
                <div className="circle">
                    <span className="aarrow aarrow--right aarrow--white"/>
                </div>
                <label className="active">Move the image on the retina</label>
            </>
        },
        {
            instruction: 'Fifth Etiam gravida est vel dolor finibus, at gravida nibh consectetur. Nunc feugiat ut metus at vestibulum.',
        },
    ]

    // HOOKS
    useEffect(() => {
        setItem(appState.education.item);
    }, [appState.education.item])

    useEffect(() => {
        setLens(appState.education.lens);
    }, [appState.education.lens])

    // METHODS
    

    return (
        <div className={`education-content ${appState.education.environment === 'night' ? 'education-content--inverted' : ''}`}>
            {children}

            {
                appState.education.visualDefect1 && 
                <EducationStep steps={educationSteps}/>
            }

            {
                (!appState.education.visualDefect1) &&
                <label className="education-content__message">You must enter one visual defect</label>
            }
            
        </div>
    )
}

export default EducationContent;