import React from 'react';
import educationImageA from '../../../assets/images/education/education-a.png';
import educationImageB from '../../../assets/images/education/education-b.png';
import educationImageC from '../../../assets/images/education/education-c.png';
import eye from '../../../assets/images/eye.png';
import lens from '../../../assets/svg/logoVerresEnhanceBlue.svg';


const EducationLanding = ({
    onSelect
}) =>  {
    // VARIABLES
    const cards = [
        {
            image: educationImageA,
            label: 'Eye',
            icon: eye,
            id: 'eye'
        },
        {
            image: educationImageB,
            label: 'Lens',
            icon: lens,
            // id: 'lens'
        },
        {
            image: educationImageC,
            label: 'Frame advices',
            // icon: '[x]',
            soon: true,
        }
    ]

    // HOOKS

    // METHODS
    const selectCard = (card) => {
        if(card.id && onSelect)onSelect(card)
    }

    return (
        <div className="education-landing">
            <div className="education-landing__instruction">
                <h1 className="education-landing__instruction-head">
                    Lorem ipsum in dolor en sit amet?
                </h1>
                <p className="education-landing__instruction-text">
                    Etiam gravida est vel dolor finibus, at gravida nibh consectetur. Nunc feugiat ut metus at vestibulum. Aliquam in condimentum eros. Aliquam mollis nibh arcu.
                </p>
            </div>
            <div className="education-landing__card-container">

                {cards.map((card, index) => {
                    return <div className="education-landing__card" onClick={() => selectCard(card)} key={index}>                    
                            <div className="education-landing__card-background">
                                <img src={card.image} alt=""/>

                                <div className={`cover ${card.soon ? 'cover--soon' : ''}`}>                                    
                                    <div className="info">
                                        {
                                            card.icon &&
                                            <span>
                                                <img src={card.icon} alt="" />
                                            </span>
                                        }
                                        <label>{card.label}</label>

                                        { card.soon ? <button>Coming soon</button> : ''}
                                    </div>
                                </div>
                            </div> 
                        </div>  
                })} 

            </div>
        </div>
    )
}

export default EducationLanding;



