import React, { useContext } from 'react';
import { AppContext } from "../context/AppContext";
import Icon from "../components/Icon";
import Button from "../components/Button";

const NEFinishedContainer = () => {
    const { appState, setAppState } = useContext(AppContext);
    const { needExplorerController } = appState

    const handleSave = () => {
        setAppState({
            ...appState,
            needExplorerController: {
                ...needExplorerController,
                pageProgress: 'result-page'
            }
        })
    }

    const handleModify = () => {
        setAppState({
            ...appState,
            needExplorerController: {
                ...needExplorerController,
                activeCategory: 'outdoorActivities',
                pageProgress: 'questionnaire-page'
            }
        })
      }

    return (
        <div className="need-explorer-container__finished">
          <Icon iconKey="check-icon"></Icon>
          <h1 className="header-text">Finished</h1>
          <Button
              className=" next-btn"
              text="Save your anamnesis"
              icon="arrow-white-blue"
              handler={handleSave}
          ></Button>
          <span className="result-text" onClick={handleModify}>Modify my answers</span>
        </div>
    )
}

export default NEFinishedContainer;