import React from 'react';
import Icon from "./Icon";
import { useLocation, Link } from 'react-router-dom';
import { routes } from "../constants/routes"; 

const BreadCrumbs = () => {  
  const Crumbs = () =>  {
    const location = useLocation();
    
    switch (location.pathname) {
      case routes.HOMEPAGE.path:
        return <HomeCrumb active={true} />;
      case routes.LPB.path:
        return (
          <React.Fragment>
            <HomeCrumb />
            <DynamicCrumb
              text="Lens Proposal Builder"
              active={true}
              route={routes.LPB.path}
            />
          </React.Fragment>
        );
      case routes.LENS_CONSULTATION.path:
        return (
          <React.Fragment>
            <HomeCrumb />
            <DynamicCrumb text="Lens Proposal Builder" route={routes.LPB.path} />
            <DynamicCrumb
              text="Lens Consultation"
              active={true}
              route={routes.LENS_CONSULTATION.path}
            />
          </React.Fragment>
        );
      case routes.PACKAGE_OFFER.path:
        return (
          <React.Fragment>
            <HomeCrumb />
            <DynamicCrumb text="Lens Proposal Builder" route={routes.LPB.path} />
            <DynamicCrumb
              text="Lens Consultation"
              route={routes.LENS_CONSULTATION.path}
            />
            <DynamicCrumb
              text="Packages Offer"
              active={true}
              route={routes.PACKAGE_OFFER.path}
            />
          </React.Fragment>
        );
      case routes.NEED_EXPLORER.path:
        return (
          <React.Fragment>
            <HomeCrumb />
            <DynamicCrumb text="Lens Proposal Builder" route={routes.LPB.path} />
            <DynamicCrumb
              text="Need Explorer"
              active={true}
              route={routes.NEED_EXPLORER.path}
            />
          </React.Fragment>
        );
      case routes.PRESCRIPTION.path:
        return (
          <React.Fragment>
            <HomeCrumb />
            <DynamicCrumb text="Lens Proposal Builder" route={routes.LPB.path} />
            <DynamicCrumb
              text="Prescription"
              active={true}
              route={routes.PRESCRIPTION.path}
            />
          </React.Fragment>
        );
      case routes.LENS_THICKNESS.path:
        return (
          <React.Fragment>
            <HomeCrumb />
            <DynamicCrumb text="Lens Proposal Builder" route={routes.LPB.path} />
            <DynamicCrumb
              text="Lens Consultation"
              route={routes.LENS_CONSULTATION.path}
            />
            <DynamicCrumb
              text="Lens Thickness"
              active={true}
              route={routes.LENS_THICKNESS.path}
            />
          </React.Fragment>
        );
      case routes.LENS_SUMMARY.path:
        return (
          <React.Fragment>
            <HomeCrumb />
            <DynamicCrumb text="Lens Proposal Builder" route={routes.LPB.path} />
            <DynamicCrumb
              text="Lens Consultation"
              route={routes.LENS_CONSULTATION.path}
            />
            <DynamicCrumb text="Lens Thickness" route={routes.LENS_THICKNESS.path} />
            <DynamicCrumb
              text="Summary"
              active={true}
              route={routes.LENS_SUMMARY.path}
            />
          </React.Fragment>
        );
      case routes.MEASURES.path:
        return (
          <React.Fragment>
            <HomeCrumb />
            <DynamicCrumb text="Lens Proposal Builder" route={routes.LPB.path} />
            <DynamicCrumb
              text="Measures"
              active={true}
              route={routes.MEASURES.path}
            />
          </React.Fragment>
        );
      case routes.LEGAL_NOTICES.path:
        return (
          <React.Fragment>
            <HomeCrumb />
            <DynamicCrumb
              text="Legal Notices"
              active={true}
              route={routes.LEGAL_NOTICES.path}
            />
          </React.Fragment>
        );
      case routes.MY_COMPANION.path:
        return (
          <React.Fragment>
            <HomeCrumb />
            <DynamicCrumb
              text="My Companion"
              active={true}
              route={routes.MY_COMPANION.path}
            />
          </React.Fragment>
        );
      case routes.PRACTICE_ID.path:
        return (
          <React.Fragment>
            <HomeCrumb />
            <DynamicCrumb
              text="Practice ID"
              active={true}
              route={routes.PRACTICE_ID.path}
            />
          </React.Fragment>
        );
      case routes.DELIVERY.path:
        return (
          <React.Fragment>
            <HomeCrumb />
            <DynamicCrumb text="Lens Proposal Builder" route={routes.LPB.path} />
            <DynamicCrumb
              text="Delivery"
              active={true}
              route={routes.DELIVERY.path}
            />
          </React.Fragment>
        );

      default:
        return "";
    }
  }

  return (
    <div className="breadcrumbs">
      {Crumbs()}
    </div>
  );
};

const HomeCrumb = ({active}) => {
  return (
    <div className="breadcrumbs__home">
      <Link to={routes.HOMEPAGE.path}>
        <Icon iconKey={`${active ? 'homeIcon-active' : 'homeIcon'}`}/>
      </Link>
    </div>
  )
}

const DynamicCrumb = ({text, active, route}) => {
  return (
    <Link to={route} style={{ textDecoration: 'none' }}>
      <div className="breadcrumbs__crumb">
          <Icon iconKey="lessThanIcon"/>
          <div>
            <h6 className={`crumb-text ${active && 'active'}`}>{text}</h6>
            {active && <div className="active-circle"></div>}
          </div>
          
          <div></div>
      </div>
    </Link>
  )
}
 
export default BreadCrumbs;