import React,{ useContext } from 'react';
import { AppContext } from "../context/AppContext";
import General from './NeedExplorerCategories/General';
import ScreenUsage from './NeedExplorerCategories/ScreenUsage';
import Transports from './NeedExplorerCategories/Transports';
import Indoor from './NeedExplorerCategories/Indoor';
import OutdoorActivities from './NeedExplorerCategories/OutdoorActivities';

const NEQuestionsContainer = () => {
    const { appState } = useContext(AppContext);
    const { needExplorerController } = appState;
    const { activeCategory } = needExplorerController;

    switch(activeCategory){
        case 'general': return <General/>;
        case 'screenUsage': return <ScreenUsage/>;
        case 'transports': return <Transports/>;
        case 'indoor': return <Indoor/>;
        case 'outdoorActivities': return <OutdoorActivities/> 

        default: return '';
    }
}

export default NEQuestionsContainer;