import React, { useState, useContext} from "react";
import LPBButtons from "../components/LPBButtons";
import Button from "../components/Button";
import HalfCircleProgress from "../components/HalfCircleProgress";
import LensSlider from "../components/LensSlider";
import Precision from "../components/Precision";
import { AppContext } from "../context/AppContext";

const LTContainer = () => {
  const [state, setState] = useState({ displayPrescription: false,  });
  const { appState, setAppState } = useContext(AppContext);
  const { prescriptionController } = appState;
  const { active } = prescriptionController;
  const { displayPrescription } = state;

  const onChangeContainer = () =>{
    setState({
      ...state, displayPrescription: !displayPrescription
    })
  }
  const handlePrecisionClick = (precision) => {
    setAppState({
      ...appState,
      prescriptionController: {
        ...prescriptionController,
        active: precision,
      },
    });
  };
    return (
      <div className="lt-right">
        <div className={`${!displayPrescription ? "display" : "display-none"}`}>
          <LPBButtons
            title={"Prescription"}
            statusText={"Completed"}
            selector={"blue"}
            displayExtra={"display"}
            icon={"modifyIcon"}
            displayFooter={"hide"}
            // route={routes.PRESCRIPTION}
            handler={onChangeContainer}
          />
          <div className="lt-right__report">
            <div className="lt-right__report--card">
              <div className="text">Lens Thickness</div>
              <div className="lt-right__report--value">1.6</div>
            </div>
            <div className="lt-right__report--summary">
              <div className="lt-right__report--summary tri-layout">
                <HalfCircleProgress value={-50} label="Weight" />
                <HalfCircleProgress value={-5} label="Center Thickness*" />
                <HalfCircleProgress value={+2} label="Edges Thickness*" />
              </div>
              <LensSlider />
              <div className="lt-right__report--summary footer">
                <div className="text">*Compared to the reference index</div>
                <Button
                  className="next-btn"
                  text="View more details"
                  icon="arrow-white-blue"
                ></Button>
              </div>
            </div>
          </div>
        </div>
        <div className={`${displayPrescription ? "display" : "display-none"}`}>
          <Button
            className="basic-icon-btn back-btn"
            text="Back"
            icon="start-arrow"
            handler={onChangeContainer}
          ></Button>
          <div className="prescription-container">
            <div className="navigator">
              <div className="container">
                <div className={`container__btn ${active === "standard" ? 'active' : ''}`}
                    onClick={() => handlePrecisionClick('standard')}
                >
                  Standard Precision
                </div>
                <div className={`container__btn ${active === "ava" ? 'active' : ''}`}
                    onClick={() => handlePrecisionClick('ava')}
                >
                  AVA Precision
                </div>
              </div>
            </div>
            <div className="sca">
              <Precision
                title="Sphere (SPH)"
                odValue="+4,27"
                osValue="+1,52"
              />
              <Precision
                title="Cylinder (CYL)"
                odValue="+3,22"
                osValue="+2,51"
              />
              <Precision
                title="Axis (AXE)"
                odValue="+0,24"
                osValue="+2,28"
              />
            </div>
            <div className="add">
              <div className="add__title">
                Addition (ADD)
              </div>
              <div className="add__tbox">
                +0,28
              </div>
            </div>
          </div>
        </div>
      </div>
    );
}
 
export default LTContainer;