import React, { useContext } from "react";
import DeliveryModalContainer from "./container/DeliveryModalContainer";
import DeliverySummaryContainer from "./container/DeliverySummaryContainer";
import Modal from '../../components/Modal';
import Button from '../../components/Button';
import { AppContext } from "../../context/AppContext";

const Delivery = () => {
  const { appState, setAppState } = useContext(AppContext);
  const { deliveryController, showModalDim } = appState
  const { exportModalShow } = deliveryController

  const renderExportModalContent = () =>{
    return (
      <div className="modal__content">
        <div className="modal__content--header">
          <h2 className="title">Export</h2>
          <Button
            className="icon-btn close"
            icon="close-button"
            handler={handleExportModal}
          ></Button>
        </div>
        <div className="modal__content--btn">
          <Button
            className=" next-btn summary-btn export"
            text="QR Code"
            icon="qr-icon"
            iconAround={false}
          ></Button>
          <Button
            className=" next-btn summary-btn export"
            text="PMS"
            icon="pms-icon"
            iconAround={false}
          ></Button>
          <Button
            className=" next-btn summary-btn export"
            text="Print"
            icon="print-icon"
            iconAround={false}
          ></Button>
          <Button
            className=" next-btn summary-btn export"
            text="Mail"
            icon="mail-icon"
            iconAround={false}
          ></Button>
        </div>
      </div>
    );
  }

  const handleExportModal = () => {
    setAppState({
        ...appState,
        showModalDim: !showModalDim,
        deliveryController: {
            ...deliveryController,
            exportModalShow: !exportModalShow
        }
    })
  }

  return (
    <div className="delivery">
         <DeliveryModalContainer/>
         <DeliverySummaryContainer/>
         <Modal
              className="delivery__export-modal modal-simple"
              isShow={exportModalShow}
              content={renderExportModalContent()}
          />
    </div>
  );
};

export default Delivery;
