import React, { useEffect, useState } from 'react';
import './dropdown.scss';

const DropDown = ({
    options,
    onSelect,
    value,
    defaultValue
}) =>  {
    // VARIABLES
    const [showOptions, setShowOptions ] = useState(false);
    const [ data, setData ] = useState(value);

    const B = 'dropdown';
    const E = (el, mod) => {
        const element = `${B}__${el}`;
        let modifier = '';
        if(mod) {
            modifier = mod.map( x => `${element}--${x}`).join(" ");
        }
        return `${element} ${modifier}`;
    }

    // HOOKS
    useEffect(() => {
        if(onSelect && data)onSelect(data);
    }, [data]);

    useEffect(() => {
        setData(value);
    }, [value]);

    // METHODS
    const toggleOptions = (show) => {   
        const delay = show ? 0 : 200;
        setTimeout(() => {
            setShowOptions(show)
        }, delay);
    }

    const selectOption = (option) => {
        // if(onSelect)onSelect(option);
        setData(option);
    }

    const focusHandler = () => {
        toggleOptions(showOptions ? false : true);        
    }

    const blurHandler = () => {
        toggleOptions(false)
    }

    return (
        <div className={B}>
            
            <button 
                className={E('value', [data ? 'with-data' : ''])}>
                {data && data.label}
                {!data && defaultValue && defaultValue.label}
            </button>            
            <button 
                onBlur={blurHandler}
                onClick={focusHandler}
                className={E('arrow')}>                
                <span className={`arrow arrow--${ showOptions ? 'up' : 'down' }`}></span>
            </button>

            {
                showOptions && 
                <ul className={E('options')}>
                    {
                        options && options.map( (opt, index) => (
                            <li onClick={() => selectOption(opt)} key={index}>
                                {opt.label}
                            </li>
                        ))
                    }
                </ul>
            }

        </div>
    )
}

export default DropDown;