import React, { useContext } from 'react';
import { AppContext } from "../context/AppContext";
import Button from '../components/Button';
import MeasureSummaryContainer from "../containers/MeasureSummaryContainer";
import TextBox from '../components/TextBox';
import Modal from '../components/Modal';
import { useHistory } from 'react-router-dom';
import { routes } from "../constants/routes"; 

const Measures = () => {
    const history = useHistory();
    const { appState, setAppState } = useContext(AppContext);
    const { measuresController, showModalDim } = appState;
    const { pageProgress, modalActive, showSummary, isImportModalShow } = measuresController;

    const renderPage = () => {
        switch(pageProgress){
          case 1 : return renderBG('measures-page__step1')
          case 2 : return renderBG('measures-page__step2')
          case 3 : return renderBG('measures-page__step3')
          default: return ''
        }
    }

    const renderBG = (classImage) => {
        return <div className={classImage}></div>
    }

    const renderProgress = () => {
        return (
            <ul className="measures-page__progress">
                <li onClick={() => handleStepClick(1)}
                    className={`measures-page__progress--step ${pageProgress === 1 ? 'active': ''}`}>
                    <div className="content">
                        {
                            pageProgress === 1 ? 
                                <React.Fragment>
                                    <h5>Measure 1</h5>
                                    <h3>Nulla in consequat ante</h3>
                                </React.Fragment> :
                            pageProgress > 1 ? <h4>1</h4> : ""
                        }
                    </div>
                </li>
                <li onClick={() => handleStepClick(2)}
                    className={`measures-page__progress--step ${pageProgress === 2 ? 'active': ''}`}>
                    <div className="content">
                        {
                            pageProgress === 2 ? 
                                <React.Fragment>
                                    <h5>Measure 2</h5>
                                    <h3>Nulla in consequat ante</h3>
                                </React.Fragment> :
                                pageProgress > 2 ? <h4>2</h4> : ""
                        }
                    </div>
                </li>
                <li onClick={() => handleStepClick(3)}
                    className={`measures-page__progress--step ${pageProgress === 3 ? 'active': ''}`}>
                    <div className="content">
                        {
                            pageProgress === 3 ? 
                                <React.Fragment>
                                    <h5>Measure 3</h5>
                                    <h3>Nulla in consequat ante</h3>
                                </React.Fragment> :
                                pageProgress > 3 ? <h4>3</h4> : ""
                        }
                    </div>
                </li>
            </ul>
        )
    }

    const renderModalPage = () => {
        const content = pageProgress === 1 ? {
            headerContent: "Quisque non molestie tortor",
            subContent: "Vivamus quis gravida erat. Curabitur a nibh et lectus sodales aliquet quis in metus. Nunc luctus neque lobortis elit consequat rhoncus nec at dui. "
        } : pageProgress === 2 ? {
            headerContent: "Duis varius tortor et libero bibendum",
            subContent: "Quisque at eros malesuada turpis bibendum imperdiet at consectetur ex. Sed dictum cursus felis vel consectetur."
        } : pageProgress === 3 ? {
            headerContent: "Pellentesque rutrum odio augue",
            subContent: "Aenean commodo massa a leo dignissim, sit amet accumsan turpis hendrerit. Maecenas iaculis auctor enim, vel accumsan dui tincidunt nec."
        } : {}

        const { headerContent, subContent } = content

        return (
            <div className="measures-page__modal">
                <div className="measures-page__modal--box">
                    <div className="mp-header">
                        <p>Pair 1</p>
                        <Button 
                        className="basic-icon-btn simple compact"
                        icon="icon-import"
                        text=""
                        handler={handleImport}
                        ></Button>
                    </div>
                    <div className="mp-content">
                        <p className="headercontent">{headerContent}</p>
                        <p className="subcontent">{subContent}</p>
                        {renderTextBoxGroup()}
                    </div>
                    <div className="mp-footer">
                        <Button
                        className="next-btn compact"
                        text={`${pageProgress === 3 ? 'Save & Next' : 'Next' }`}
                        icon="arrow-white-blue"
                        handler={pageProgress === 3 ? handleSaveAndNext : handleNextPage}
                        ></Button>
                    </div>
                </div>
            </div>
        )
    }

    const renderTextBoxGroup = () => {
        switch(pageProgress) {
            case 1: return (
                <React.Fragment>
                    <div className="textbox-group">
                        <label>Price</label>
                        <TextBox
                            value="120"
                            className="mp-price"
                        />
                    </div>
                    <div className="textbox-group">
                        <label>A &nbsp; <div className="rectangle"></div>&nbsp; B</label>
                        <TextBox
                            value="47"
                            className="mp-a"
                        />
                        <TextBox
                            value="21"
                            className="mp-b"
                        />
                    </div>
                    <div className="textbox-group">
                        <label>D</label>
                        <TextBox
                            value="140"
                            className="mp-d"
                        />
                    </div>
                </React.Fragment>
            )
            case 2: return (
                <React.Fragment>
                    <div className="measures2-tbox">
                        <div className="left">
                            <label className="left-label">Left eye</label>
                            <label>PD</label>
                            <TextBox
                                value="23mm"
                                className="mp-left-eye"
                            />
                            <label>Height</label>
                            <TextBox
                                value="23mm"
                                className="mp-left-eye"
                            />
                        </div>
                        <div className="right">
                            <label className="right-label">Right eye</label>
                            <label>PD</label>
                            <TextBox
                                value="23mm"
                                className="mp-left-eye"
                            />
                            <label>Height</label>
                            <TextBox
                                value="23mm"
                                className="mp-left-eye"
                            />
                        </div>
                    </div>
                </React.Fragment>
            )
            case 3: return (
                <React.Fragment>
                    <div className="textbox-group">
                        <label>Pantoscopic Angle</label>
                        <TextBox
                            value="0"
                            className="mp-data-name-1"
                        />
                    </div>
                    <div className="textbox-group">
                        <label>Wrap Angle</label>
                        <TextBox
                            value="0"
                            className="mp-data-name-2"
                        />
                    </div>
                    <div className="textbox-group">
                        <label>Vertex distance</label>
                        <TextBox
                            value="0"
                            className="mp-data-name-2"
                        />
                    </div>
                    <div className="textbox-group">
                        <label>Reading distance</label>
                        <TextBox
                            value="0"
                            className="mp-data-name-2"
                        />
                    </div>
                </React.Fragment>
            )
            default: return null
        }
    }

    const renderModalContent = () => {
        return (
            <div className="measures-modal-content">
                <div className="btn-header">
                    <Button
                        className="icon-btn close"
                        icon="close-button"
                        handler={handleCloseModal}
                    ></Button>
                </div>
                <h2 className="measures-modal-header">Measures</h2>
                <Button 
                  className="basic-icon-btn simple white compact"
                  text="Add a new pair   +"
                  handler={handleAddNewPair}
                ></Button>
                <a href="/" className="link-backToLens" onClick={handleLPB}>Back to Lens Proposal Builder</a>
            </div>
        )
    }

    const renderModalImportContent = () => {
        return (
            <div className="measures-import-modal-content">
                <div className="btn-header">
                    <Button
                        className="icon-btn close"
                        icon="close-button"
                        handler={handleImport}
                    ></Button>
                </div>
                <h2 className="measures-import-modal-header">Import</h2>
                <div className="panel-import-buttons">
                    <Button
                        className="basic-icon-btn white simple"
                        text="QR Code"
                        icon="qr-code"
                    ></Button>
                    <Button
                        className="basic-icon-btn white simple"
                        text="ID"
                        icon="id"
                        handler={()=>{}}
                    ></Button>
                </div>
            </div>
        )
    }

    const handleNextPage = () => {
        setAppState({
            ...appState,
            measuresController:{
                ...measuresController,
                pageProgress: pageProgress+1
            }
        })
    }

    const handleSaveAndNext = () => {
        setAppState({
            ...appState,
            showModalDim: true,
            measuresController:{
                ...measuresController,
                modalActive: true
            }
        })
    }

    const handleCloseModal = () => {
        setAppState({
            ...appState,
            showModalDim: false,
            measuresController:{
                ...measuresController,
                modalActive: false
            }
        })
    }

    const handleStepClick = (step) => {
        setAppState({
            ...appState,
            measuresController:{
                ...measuresController,
                pageProgress: step
            }
        })
    }

    const handleAddNewPair = () => {
        setAppState({
            ...appState,
            showModalDim: false,
            measuresController:{
                ...measuresController,
                modalActive: false,
                showSummary: true
            }
        })
    }

    const handleLPB = (e) => {
        e.preventDefault();
        setAppState({
            ...appState,
            showModalDim: false,
            measuresController:{
                ...measuresController,
                modalActive: false
            }
        })

        history.push(routes.LPB.path);
    }

    const handleImport = () => {
        setAppState({
            ...appState,
            showModalDim: !showModalDim,
            measuresController:{
                ...measuresController,
                isImportModalShow: !isImportModalShow
            }
        })
    }

    return !showSummary ? (
            <div className="measures-page">
                { renderPage() }
                { renderProgress() }
                { renderModalPage() }
                <Modal className="modal-simple measures-modal-content" isShow={modalActive} content={renderModalContent()} />
                <Modal className="modal-simple measures-modal-content" isShow={isImportModalShow} content={renderModalImportContent()} />
            </div>
        ) : (
            <div className="measures-page">
                <MeasureSummaryContainer />
            </div>
        )
}

export default Measures;