import React from 'react';
import IconText from '../../../components/IconText';

const ProductTable = ({
    productCategory,
    products
}) => {

    const renderProductTable = () => {

        var products_jsx = products.map((i, j)=> {
            var classVariation = j === 0 ? 'firstTable' : 
                                 j === (products.length - 1) ? 'lastTable' : 'midTable' 
            var is1only = (products.length - 1) === 0 

            return (
                <div className={`product-table__table ${is1only ? '' : classVariation}`} key={j}>
                    <div className="product-table__table--header">
                        {i.productName}
                    </div>
                    <div className="product-table__table--content">
                        {renderIconTexts(i.iconTexts)}
                    </div>
                </div>
            )
        })

        return products_jsx
    }

    const renderIconTexts = (iconTexts) => {
        var iconTexts_jsx = iconTexts.map((i, c)=> {
            return (
                <IconText 
                    iconKey={i.iconKey}
                    text={i.text}
                    key={c}
                />
            )
        })

        return iconTexts_jsx
    }

    return (
        <div className="product-table">
            <p className="product-table__title">
                {productCategory}
            </p>
            {renderProductTable()}
        </div>
    )
}

export default ProductTable