import React, { useState } from 'react';
import TextBoxOneBorder from '../components/TextBoxOneBorder';
import Button from '../components/Button';

const PracticeID = () => {
    const [localState, setLocalState] = useState({
        storeName: {
            value: "",
            error: false
        },
        phone: {
            value: "",
            error: false
        },
        address: {
            value: "",
            error: false
        },
        mailAddress: {
            value: "",
            error: false
        },
        essilorCompanion: {
            value: "",
            error: false
        },
        essilorCompanionVr: {
            value: "",
            error: false
        }
    })

    const { storeName, phone, address, mailAddress, essilorCompanion, essilorCompanionVr } = localState

    const handleTextBoxChange = (e) => {
        setLocalState({
            ...localState,
            [e.target.name]: {
                ...[e.target.name],
                value: e.target.value
            }
        })
    }

    return (
        <div className="practice-id">
            <img className="practice-id__hero" alt=""></img>
            <div className="practice-id__forms">
                <h1>Global Information</h1>
                <TextBoxOneBorder
                    value={storeName.value}
                    name="storeName"
                    placeholder="Name: Stores name"
                    label="Store Name"
                    handler={handleTextBoxChange}
                />
                <TextBoxOneBorder
                    value={phone.value}
                    name="phone"
                    placeholder="Phone: Type here"
                    label="Phone"
                    handler={handleTextBoxChange}
                />
                <TextBoxOneBorder
                    value={address.value}
                    name="address"
                    placeholder="Address: Type here"
                    label="Address"
                    handler={handleTextBoxChange}
                />
                <TextBoxOneBorder
                    value={mailAddress.value}
                    name="mailAddress"
                    placeholder="Mail address: Type here"
                    label="Mail address"
                    handler={handleTextBoxChange}
                />
            <h1>Licences numbers</h1>
                <TextBoxOneBorder
                    value={essilorCompanion.value}
                    name="essilorCompanion"
                    placeholder="Essilor Companion: Type here"
                    label="Essilor Companion"
                    handler={handleTextBoxChange}
                />
                <TextBoxOneBorder
                    value={essilorCompanionVr.value}
                    name="essilorCompanionVr"
                    placeholder="Essilor Companion VR: Type here"
                    label="Essilor Companion VR"
                    handler={handleTextBoxChange}
                />
                <Button
                    className="next-btn compact pid-btn"
                    text="Save"
                    icon="arrow-white-blue"
                ></Button>
            </div>

        </div>
    )
}

export default PracticeID;