import React, { useContext } from 'react';
import { AppContext } from "../../../context/AppContext";
import Modal from "../../../components/Modal";
import Button from "../../../components/Button";

const DeliveryModalContainer = () => {
    const { appState, setAppState } = useContext(AppContext);
    const { deliveryController } = appState
    const { pageProgress, sectionCoordinates } = deliveryController

    const handleViewSummary = () => {
        setAppState({
            ...appState,
            deliveryController: {
                ...deliveryController,
                pageProgress: 'summary-page',
                summaryPage: 'Prescriptions'
            }
        })
    }

    const checkIsSummaryOpen = () => {
        return pageProgress === 'summary-page' ? 'dimBG' : ''
    }

    const handleValidate = () =>{
        var vt = document.getElementById("VisionTests")
        vt.scrollIntoView({
            behavior: "smooth",
            block: "start",
        });

        setAppState({
            ...appState,
            deliveryController: {
                ...deliveryController,
                pageProgress: 'summary-page',
                summaryPage: 'VisionTests',
                sectionCoordinates: {
                    ...sectionCoordinates,
                    visionTest: {
                        section: sectionCoordinates.visionTest.section,
                        active: true
                    },
                    prescription: {
                        section: sectionCoordinates.prescription.section,
                        active: false
                    },
                }
            }
        })
    }

    const renderModalContent = () => {
        return (
            <React.Fragment>
                <h1 className="content-header">
                    Summary
                </h1>
                <div className="content-body">
                    <div className="item">
                        <div className="label">Pair 1</div>
                        <h2 className="item-name">Progressive vision</h2>
                        <p className="item-price">450 </p>
                    </div>
                    <div className="item">
                        <div className="label">Pair 1</div>
                        <h2 className="item-name">Sun vision</h2>
                        <p className="item-price">450 </p>
                    </div>
                    <div className="item">
                        <div className="label no-border">Total</div>
                        <p className="item-price">700 </p>
                    </div>
                </div>
                <div className="content-footer">
                    <Button
                        className="basic-icon-btn compact white-bg bordered"
                        text="View more details"
                        icon="arrow-white-blue"
                        handler={handleViewSummary}
                    ></Button> 
                    <Button
                        className="next-btn compact"
                        text="Validate"
                        icon="arrow-white-blue"
                        handler={handleValidate}
                    ></Button> 
                </div>
            </React.Fragment>
        )
    }

    return (
        <div className={`delivery__modal-page ${checkIsSummaryOpen()}`}>
            <Modal 
                isShow={true}
                content={renderModalContent()}
                className="delivery__modal-page--modal-content"
            />
        </div>
    )
}

export default DeliveryModalContainer;