import React, { Fragment } from 'react';
import SummaryList from './SummaryList';

const SummaryListGroup = ({
    items
}) => {
    const renderList = () => {
        const summaryLists = items.map((i,j)=>{
            const { question, answers } = i;

            return (
                <Fragment key={j}>
                    <SummaryList
                        question = { question }
                        answers = { answers }
                    />
                </Fragment>
            )
        })

        return summaryLists
    }

    return (
        <div className="summary-lists">
            {renderList()}
        </div>
    )
}

export default SummaryListGroup