import React, { useEffect, useState } from 'react';
import './toggleRadio.scss';

const ToggleRadio = ({
    value = 'off',
    onChange
}) =>  {
    // VARIABLES

    const B = 'toggle-radio';
    const E = (el, mod) => {
        const element = `${B}__${el}`;
        let modifier = '';
        if(mod) {
            modifier = mod.map( x => `${element}--${x}`).join(" ");
        }
        return `${element} ${modifier}`;
    }

    // METHODS
    const click = () => {
        const newValue = value === 'on' ? 'off' : 'on';
        if(onChange)onChange(newValue);
        value = newValue;
    }

    return (
        <div className={`${B} ${B}--${value}`} onClick={click}>
            <span className={E('slide')}></span>
        </div>
    )
}

export default ToggleRadio;